import React, { useState } from "react";
import PropTypes from "prop-types";
import { Button, Select } from "antd";
import { PlusCircleOutlined, DeleteOutlined } from "@ant-design/icons";
import { MergeByConditionOperators } from "../../constants";
import { trimStartDollarSign } from "../../utils/common";
import ConditionForm from "../../components/ConditionForm";
import "./condition-groups.less";

const SelectOption = Select.Option;

const columns = [
  {
    name: "uuid",
    type: "string",
  },
  {
    name: "credit",
    type: "number",
  },
  {
    name: "lastLogin",
    type: "timestamp",
  },
];

const ConditionGroupItem = (props) => {
  const operator = MergeByConditionOperators.find(
    (item) => item.key === props.conditionGroup[1]
  );
  return (
    <div className="condition-group-item">
      <div className="condtion-group-item__expr">
        {trimStartDollarSign(props.conditionGroup[0])} {operator.name}{" "}
        {trimStartDollarSign(props.conditionGroup[2])}
      </div>
      <div
        className="condition-group-item__delete-icon"
        onClick={props.onDelete}
      >
        <DeleteOutlined />
      </div>
    </div>
  );
};

const ConditionGroups = (props) => {
  const { property, groupKey, value: conditionGroups } = props;
  const [showInput, setShowInput] = useState(false);
  const [conditionFormVisible, setConditionFormVisible] = useState(false);

  const toggleConditionForm = () => {
    setConditionFormVisible(!conditionFormVisible);
  };

  const hideConditionForm = () => {
    setConditionFormVisible(false);
  };

  const handleDelete = (i) => () => {
    conditionGroups[0][1].splice(i, 1);
    props.onValueChange(groupKey, property.key)(conditionGroups);
  };

  const handleAdd = (condition) => {
    if (!!conditionGroups) {
      conditionGroups[0][1].push([
        condition.leftColumn,
        condition.operator,
        condition.rightColumn,
      ]);
      props.onValueChange(groupKey, property.key)(conditionGroups);
    } else {
      props.onValueChange(
        groupKey,
        property.key
      )([
        [
          "and",
          [[condition.leftColumn, condition.operator, condition.rightColumn]],
        ],
      ]);
    }
    setConditionFormVisible(false);
  };

  return (
    <div className="config-group__setting-block">
      <div className="setting-label">
        <span>{property.label}</span>
      </div>
      <div className="setting-input-block condition-group-input">
        <div className="condition-group-list">
          {conditionGroups
            ? conditionGroups[0][1].map((conditionGroup, i) => (
                <ConditionGroupItem
                  key={i}
                  conditionGroup={conditionGroup}
                  onDelete={handleDelete(i)}
                />
              ))
            : null}
        </div>
        <Button
          className="condition-group-input__add-condition-btn"
          icon={<PlusCircleOutlined />}
          onClick={toggleConditionForm}
        >
          Add condition
        </Button>
        {conditionFormVisible ? (
          <ConditionForm
            columns={columns}
            onCancel={hideConditionForm}
            onFinish={handleAdd}
          />
        ) : null}
      </div>
    </div>
  );
};

ConditionGroups.propTypes = {
  groupKey: PropTypes.string.isRequired,
  property: PropTypes.object.isRequired,
  onValueChange: PropTypes.func.isRequired,
};

export default ConditionGroups;
