import { observable, action, computed, makeObservable } from 'mobx';
import templatesJSON from '../templates';
import { MxGraph2Json, stringifyWithoutCircular } from '../lib/MxGraphUtils';

export default class TemplatesStore {
  @observable templates = templatesJSON;
  @observable currentTemplate = templatesJSON[0];

  constructor(rulesStore) {
    this.rulesStore = rulesStore;
    makeObservable(this)
  }

  @action
  previewTemplate = (template) => {
    this.currentTemplate = template;
  }

  @action
  copyTemplate = () => {
    this.rulesStore.applyTemplate(this.currentTemplate.configs, this.currentTemplate.layouts);
  }

  @action
  publishTemplate = () => {
    const template = {
      id: +new Date(),
      name: +new Date(),
      description: +new Date(),
      layouts: this.rulesStore.layouts,
      configs: this.rulesStore.configs
    }

    console.error(stringifyWithoutCircular(template.layouts));

    this.templates.push(template);
  }
}
