import SockJS from "sockjs-client";
import Stomp from "stompjs";
import { Timer } from "../utils/common";

class TopicMessageCallback {
  constructor(topic, callback) {
    this.topic = topic;
    this.callback = callback;
  }
}

export default class StompWebsocket {
  constructor(endpoint) {
    this.endpoint = endpoint;
    this.messageCallbacks = [];
    this.conn = null;
    this.topics = [];
    this.ref = 0;
    this.connected = false;
    this.rejoinTimer = null;
    this.sendBuffer = [];
  }

  /**
   * Return the next message ref, accounting for overflows
   * @returns {string}
   */
  makeRef() {
    let newRef = this.ref + 1;
    if (newRef === this.ref) {
      this.ref = 0;
    } else {
      this.ref = newRef;
    }

    return this.ref.toString();
  }

  connect(successCallback, errorCallback) {
    if (this.conn & this.connected) return;
    this.sock = SockJS("/stomp");
    this.conn = Stomp.over(this.sock);
    this.conn.connect(
      {},
      () => {
        this.onConnOpen();
        successCallback && successCallback();
      },
      (error, other) => {
        this.onConnClose(error);
        errorCallback && errorCallback(error, other);
      }
    );
  }

  reconnect() {
    // Any other stuffs should be cleaned up here?
    this.connect();
  }

  disconnect() {
    this.conn && this.conn.disconnect();
  }

  registerCallback(topic, callback) {
    if (!this.topics.includes(topic)) {
      this.topics.push(topic);
    }
    let messageCallback = new TopicMessageCallback(topic, (message) =>
      callback(message)
    );
    this.messageCallbacks.push(messageCallback);
  }

  send(topic, headers = {}, message) {
    if (this.connected) {
      this.conn.send(topic, headers, message);
    } else {
      // start timer
      this.rejoinTimer = new Timer(
        () => {
          if (this.connected) {
            this.reconnect();
          }
        },
        (tries) => {
          return [1000, 3000, 5000, 10000][tries - 1] || 10000;
        }
      );
      this.sendBuffer.push({ topic, message, headers });
    }
  }

  onConnOpen() {
    console.log("StompWebsocket connected!");
    this.connected = true;

    if (this.rejoinTimer) {
      this.rejoinTimer.reset();
    }

    // subscribe topics
    this.topics.forEach((topic) => {
      this.conn.subscribe(topic, this.onMessage.bind(this));
    });

    // flush send buffer
    console.log("flushing send buffer...");
    this.sendBuffer.forEach(({ topic, message, headers }) => {
      this.send(topic, headers, message);
    });
    this.sendBuffer = [];
  }

  onMessage(frame) {
    const topic = frame.headers.destination;
    const topicBindings = this.messageCallbacks.filter(
      (mc) => mc.topic === topic
    );
    topicBindings.forEach((messageCallback) => {
      messageCallback.callback(frame);
    });
  }

  onConnClose(error) {
    this.connected = false;
    console.log("StompWebsocket closed!");
  }
}
