import React, { Component } from 'react';
import PropTypes from 'prop-types';

import NodeDetailsPanel from '../NodeDetailsPanel';
import DetailConfigPanel from '../DetailConfigPanel';

import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';

import './index.less';

@observer
class DetailSettingBar extends Component {
  @observable currentTab = "detail";

  constructor() {
    super();
    makeObservable(this);
  }

  render() {
    let { node, findNodeById, updateNodeTransformerSetting, updateNodeDetailSetting } = this.props;

    if (!node) {
      return null;
    }

    return (
      <div className={`detail-setting-bar ${!!node ? "" : "hidden"}`}>
        <div className="detail-setting-bar__header">
          <div className={`detail-setting-bar__header--tab ${  this.currentTab == "detail" ? "activated" : "" }`}
               onClick={ this.switchTab("detail") }>
            Node details
          </div>
          <div className={`detail-setting-bar__header--tab ${  this.currentTab == "config" ? "activated" : "" }`}
               onClick={ this.switchTab("config") }>
            Configuration
          </div>
        </div>

        <div className="detail-setting-bar__body">
          {
            this.currentTab == "detail" ?
              <React.Fragment>
                <NodeDetailsPanel
                  node={node}
                  updateNodeDetailSetting={updateNodeDetailSetting}
                />
              </React.Fragment>:
              <DetailConfigPanel
                node={node}
                findNodeById={findNodeById}
                updateNodeTransformerSetting={updateNodeTransformerSetting}
              />
          }
        </div>
      </div>
    );
  }

  @action
  switchTab = (tabName) => () => {
    this.currentTab = tabName;
  }
};

DetailSettingBar.propTypes = {
  node: PropTypes.object,
  findNodeById: PropTypes.func.isRequired,
  updateNodeTransformerSetting: PropTypes.func.isRequired,
  updateNodeDetailSetting: PropTypes.func.isRequired,
};

export default DetailSettingBar;
