import React, { useState, useEffect } from 'react';
import { Tabs } from 'antd'
import { inject, observer } from 'mobx-react';
import PersonForm from './person-form';
import DataSourceForm from './data-source-form';
import AccountDetail from './account-detail';
import Person from './person';
import DataSource from './data-source';
import './index.less';

const TabPane = Tabs.TabPane;

const Admin = (props) => {
  const [ currentTab, setCurrentTab ] = useState('people');

  useEffect(() => {
    props.adminStore.fetchUsers();
    props.adminStore.fetchDataSources();
  }, [])

  const handleTabChange = (tabKey) => {
    setCurrentTab(tabKey)
  }

  const handleAddPerson = (person) => {
    props.adminStore.createUser(person)
  }

  const handleAddDataSource = (params) => {
    props.adminStore.createDataSource(params)
  }

  let formContent = null;
  if (currentTab === 'people') {
    formContent = <PersonForm onFinish={handleAddPerson} />
  } else if (currentTab === 'dataSource') {
    formContent = <DataSourceForm onFinish={handleAddDataSource} />
  } else if (currentTab === 'accountUsage') {
    formContent = <AccountDetail />
  }

  return (
    <div className="admin-container">
      <Tabs className="admin-container__tabs" type="card" onChange={handleTabChange}>
        <TabPane key="people" tab="People">
          { props.adminStore.users.map((p, i) => <Person key={i} data={p} />)}
        </TabPane>
        <TabPane key="dataSource" tab="Data Source">
          { props.adminStore.dataSources.map((ds, i) => <DataSource key={i} data={ds} />)}
        </TabPane>
        <TabPane key="accountUsage" tab="Account Usage">
        </TabPane>
      </Tabs>
      <div className='admin-container__sidebar'>
        { formContent }
      </div>
    </div>
  )
}

export default inject('adminStore')(observer(Admin));