import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { Menu, Dropdown } from 'antd';

import './index.less';

class SavedConfigsList extends Component {
  render() {
    let { configs } = this.props;

    return (
      <div className="saved-configs-list">
        {
          Object.keys(configs).map((type) => {
            return configs[type].map((config, m) => (
              <div className="saved-configs-list__config-block" key={`${type}-${m}`}>
                <div className="saved-configs-list__config-block--info-block">
                  <div className="saved-configs-list__config-block--title">{ type }</div>
                  <div className="saved-configs-list__config-block--desc">1 columns</div>
                </div>

                <Dropdown overlay={ this._renderActions(type, m) }  trigger={['click']}>
                  <i className="iconfont iconmore" />
                </Dropdown>
              </div>
            ))
          }).flat()
        }
      </div>
    );
  }

  _renderActions = (type, idx) => (
    <Menu onClick={this.doAction(type, idx)}>
      <Menu.Item key="edit" icon={ <i className="iconfont iconpencil" /> }>
        Edit
      </Menu.Item>
      <Menu.Item key="delete" icon={ <i className="iconfont iconclipboard-checked" /> }>
        Delete
      </Menu.Item>
    </Menu>
  )

  doAction = (type, idx) => (e) => {
    let action = e.key;

    switch (action) {
      case "delete":
        this.props.removeRule(type, idx);
        break;
      case "edit":
        this.props.setCurrentConfigInfo(type, idx);
        break;
    }
  }
}

SavedConfigsList.propTypes = {
  configs: PropTypes.object.isRequired,
  setCurrentConfigInfo: PropTypes.func.isRequired,
  removeRule: PropTypes.func.isRequired,
};

export default SavedConfigsList;
