import React from "react";
import { MoreOutlined, StarFilled } from "@ant-design/icons";
import { Dropdown, Menu } from "antd";

const MenuItem = Menu.Item;

const DataSource = ({ data }) => {
  const menu = (
    <Menu>
      <MenuItem>Delete</MenuItem>
    </Menu>
  );

  const dropdown = (
    <Dropdown overlay={menu} trigger={["click"]}>
      <MoreOutlined />
    </Dropdown>
  );

  return (
    <div className="data-source">
      <div className="data-source__body">
        <div className="data-source__body__name">{data.name}</div>
        <div className="data-source__body__type">{data.type}</div>
        <div className="data-source__body__created-at">Added: {data.creatdedAt}</div>
      </div>
      <div className="data-source__action">
        { dropdown }
      </div>
    </div>
  );
};

export default DataSource;
