import React from "react";
import { Form, Input, Select, Checkbox, Button } from "antd";

const FormItem = Form.Item;
const SelectOption = Select.Option;

const PersonForm = (props) => {
  const [form] = Form.useForm();

  const handleFinsh = (values) => {
    props.onFinish(values);
    console.log(values);
    form.resetFields();
  };

  const initialValues = {
    role: "dataScientist",
  };

  return (
    <div className="person-form-container">
      <div className="caption">Add person</div>
      <Form
        className="person-form"
        form={form}
        layout="vertical"
        onFinish={handleFinsh}
        initialValues={initialValues}
      >
        <FormItem name="name" label="Name" rules={[{ required: true }]}>
          <Input placeholder="Enter user name" />
        </FormItem>
        <FormItem
          name="email"
          label="Email"
          rules={[{ required: true, type: "email" }]}
        >
          <Input placeholder="Enter user email" />
        </FormItem>
        <FormItem name="role" label="Role" rules={[{ required: true }]}>
          <Select placeholder="Select user role">
            <SelectOption value="dataScientist">Data Scientist</SelectOption>
            <SelectOption value="businessUser">Bussiness User</SelectOption>
          </Select>
        </FormItem>
        <FormItem name="admin" valuePropName="checked">
          <Checkbox>Make admin</Checkbox>
        </FormItem>
        <FormItem>
          <Button type="primary" htmlType="submit">
            Add person
          </Button>
        </FormItem>
      </Form>
    </div>
  );
};

export default PersonForm;
