import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { makeObservable, observable, action, computed } from 'mobx';
import { v4 as uuidV4 } from 'uuid';
import NODES from "../../constants/Nodes";
import { MxGraph2Json, GraphX } from "../../lib/MxGraphUtils";
import { matchPath } from "react-router-dom";

import Workflow from '../../components/Workflow';
import NodesListBar from '../../components/NodesListBar';
import DetailSettingBar from '../../components/DetailSettingBar';
import PredictionDetailsPanel from '../../components/PredictionDetailsPanel';
import PublishTemplateModal from '../../components/PublishTemplateModal';

import './index.less';

@inject("rulesStore", "projectStore", "routingStore", "templatesStore")
@observer
class Workspace extends Component {
  @observable showPublishTemplateModal = false;
  @observable isEditingProjectDetail = false;

  constructor(props) {
    super(props);
    makeObservable(this);
    // UUID is used to reference to the graphx instance
    this.graphxInstanceUUID = uuidV4();
  }

  componentDidMount() {
    const projectId = this._getProjectId();
    this.props.projectStore.setCurrentProject(projectId);

    setTimeout(() => {
      this.initGraphx()
    }, 300);
  }

  // componentDidUpdate(preProps) {
  //   if (preProps.rulesStore.layouts != this.props.rulesStore.layouts) {
  //     window[this.graphxInstanceUUID].render({ layouts: this.props.rulesStore.layouts });
  //   }
  // }

  render() {
    let { rulesStore, projectStore, routingStore, templatesStore } = this.props;

    return (
      <div className="workspace">
        <section className="workspace__main">
          <div className="workspace__main--content">
            <Workflow
              currentProject={rulesStore.currentProject}
              addNewNode={rulesStore.addNewNode}
              selectNode={rulesStore.selectNode}
              connectNodes={rulesStore.connectNodes}
              removeNodes={rulesStore.removeNodes}
              updateLayouts={rulesStore.updateLayouts}
              layouts={rulesStore.layouts}
              showModal={this.showModal}
              editProjectDetail={this.setEditingProjectDetail}
            />
          </div>

          <div className="workspace__main--sidebar">
            <NodesListBar
              isShowing={this.isShowingToolbar}
              navigateTo={routingStore.push}
              publishTemplate={templatesStore.publishTemplate}
              nodes={NODES}
              graphxUUID={this.graphxInstanceUUID}
            />

            {
              this.isShowingNodeDetail ?
                <DetailSettingBar
                  node={rulesStore.getCurrentNode}
                  findNodeById={rulesStore.findNodeById}
                  updateNodeTransformerSetting={
                    rulesStore.updateNodeTransformerSetting
                  }
                  updateNodeDetailSetting={rulesStore.updateNodeDetailSetting}
                />:
                null
            }

            {
               this.isShowingPredictionDetail ?
                <PredictionDetailsPanel
                  project={ rulesStore.currentProject }
                  update={ projectStore.update }
                  delete={ projectStore.delete }
                  navigateTo={ routingStore.push }
                  editProjectDetail={this.setEditingProjectDetail}
                />:
                null
            }
          </div>
        </section>

        {!!this.showPublishTemplateModal ? (
          <PublishTemplateModal
            closeModal={this.closeModal}
            navigateTo={routingStore.push}
            publishTemplate={templatesStore.publishTemplate}
          />
        ) : null}
      </div>
    );
  }

  @action
  closeModal = () => {
    this.showPublishTemplateModal = false;
  };

  @action
  showModal = () => {
    this.showPublishTemplateModal = true;
  };

  @action
  setEditingProjectDetail = (isEditing) => {
    this.isEditingProjectDetail = isEditing;
  }

  @computed
  get isShowingPredictionDetail() {
    return this.isEditingProjectDetail;
  }

  @computed
  get isShowingNodeDetail() {
    const { getCurrentNode, currentNodeId } = this.props.rulesStore;
    return !this.isEditingProjectDetail && !!getCurrentNode;
  }

  @computed
  get isShowingToolbar() {
    const { currentNodeId, currentProject } = this.props.rulesStore;
    return !this.isEditingProjectDetail && !currentNodeId;
  }

  initGraphx() {
    const { rulesStore, projectStore } = this.props;
    window[this.graphxInstanceUUID] = new GraphX(
      document.querySelector("#workflow-canvas"),
      document.querySelector("#workflow-toolbar"),
      rulesStore.addNewNode,
      rulesStore.updateLayouts,
      rulesStore.connectNodes,
      rulesStore.selectNode,
      rulesStore.removeNodes
    );

    projectStore.setGraphXClient(window[this.graphxInstanceUUID]);

    if (rulesStore.layouts.length > 0) {
      window[this.graphxInstanceUUID].render({ layouts: rulesStore.layouts });
    }
  }

  _getProjectId = () => {
    const match = matchPath(this.props.routingStore.location.pathname, {
      path: '/prediction/:id',
      strict: true
    });
    return match && match.params.id;
  }
};

export default Workspace;
