import React, { StrictMode } from "react";
import ReactDOM from "react-dom";
import { Provider } from "mobx-react";
import { createBrowserHistory } from "history";
import { RouterStore, syncHistoryWithStore } from "mobx-react-router";
import { Router } from "react-router";
import App from "./App";
import StompWebsocket from "./services/StompWebsocket";
import RulesStore from "./stores/RulesStore";
import QueriesStore from "./stores/QueriesStore";
import { getAuthStatusFromLocalStorage } from "./utils/common";
import UserStore from "./stores/UserStore";
import TemplatesStore from "./stores/TemplatesStore";
import AdminStore from "./stores/AdminStore";
import ProjectStore from "./stores/ProjectStore";
import jsonSchemaValidator from "./utils/json-schema-validator";

import "./index.less";

let stompWebsocket = new StompWebsocket("/stomp");

const userStore = new UserStore();
const rulesStore = new RulesStore();
const projectStore = new ProjectStore(rulesStore);
const adminStore = new AdminStore();
const queriesStore = new QueriesStore();
const templatesStore = new TemplatesStore(rulesStore);

const browserHistory = createBrowserHistory();
const routingStore = new RouterStore();

projectStore.registerTopics(stompWebsocket, ["/user/topic/projects"]);
userStore.registerTopics(stompWebsocket, []);
rulesStore.registerTopics(stompWebsocket);

userStore.test_authentication().then(() => {
  stompWebsocket.connect();
});

const stores = {
  userStore,
  rulesStore,
  queriesStore,
  templatesStore,
  routingStore,
  adminStore,
  projectStore,
};

const history = syncHistoryWithStore(browserHistory, routingStore);

ReactDOM.render(
  <Provider {...stores}>
    <Router history={history}>
      <App />
    </Router>
  </Provider>,
  document.getElementById("root")
);
