import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './index.less';

export default class AlertBar extends Component {
  render() {
    let { title, desc, type } = this.props;

    return (
      <div className='alert-bar'>
        <div className='alert-bar__indicator'>
          <div className='alert-bar__indicator--status-bar' />
          <div className={`alert-bar__icon-block ${type}`} />
        </div>

        <div className='alert-bar__main'>
          <div className={`alert-bar__main--title ${type}`}>{ title }</div>
          <div className='alert-bar__main--desc'>{ desc }</div>
        </div>

        <div className='alert-bar__icon-block btn'>
          <i className='iconfont iconremove' />
        </div>
      </div>
    );
  }
};

AlertBar.propTypes = {
  title: PropTypes.string.isRequired,
  desc: PropTypes.string,
  type: PropTypes.string.isRequired,
};
