import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { makeObservable, observable, action, computed } from 'mobx';
import { observer } from 'mobx-react';

import NODES from '../../constants/Nodes';
import ProcessConfigPanel from '../ProcessConfigPanel';
import BeforeConfigPanel from '../BeforeConfigPanel';
import AfterConfigPanel from '../AfterConfigPanel';

import './index.less';

@observer
class DetailConfigPanel extends Component {
  @observable currentTab = "process";

  constructor() {
    super();
    makeObservable(this);
  }

  render() {
    return (
      <div className="detail-config-panel">
        <div className="detail-config-panel__tabs-bar">
          <div className={`detail-config-panel__tabs-bar--tab ${ this.currentTab == "process" ? "activated" : ""}`}
               onClick={ this.switchTab("process") }>
            Process
          </div>

          <div className={`detail-config-panel__tabs-bar--tab ${ this.currentTab == "before" ? "activated" : ""}`}
               onClick={ this.switchTab("before") }>
            Before
          </div>

          <div className={`detail-config-panel__tabs-bar--tab ${ this.currentTab == "after" ? "activated" : ""}`}
               onClick={ this.switchTab("after") }>
            After
          </div>
        </div>

        <div className="detail-config-panel__body">
          { this.renderPanel }
        </div>
      </div>
    );
  }

  @action
  switchTab = (tabName) => () => {
    this.currentTab = tabName;
  }

  @computed
  get renderPanel() {
    let { node, findNodeById, updateNodeTransformerSetting } = this.props;

    switch(this.currentTab) {
      case "process":
        return (
          <ProcessConfigPanel
            node={ node }
            findNodeById={ findNodeById }
            config={ node.transformer.process }
            formConfig={ NODES[node.transformer["className"]].inputFormConfig }
            updateNodeTransformerSetting={ updateNodeTransformerSetting }
          />
        );
        break;
      case "before":
        return (
          <BeforeConfigPanel
            config={node.transformer.before}
            updateNodeTransformerSetting={ updateNodeTransformerSetting }
          />
        );
        break;
      case "after":
        return (
          <AfterConfigPanel
            config={node.transformer.after}
            updateNodeTransformerSetting={ updateNodeTransformerSetting }
          />
        );
        break;
    }
  }
};

DetailConfigPanel.propTypes = {
  node: PropTypes.object.isRequired,
  findNodeById: PropTypes.func.isRequired,
  updateNodeTransformerSetting: PropTypes.func.isRequired,
};

export default DetailConfigPanel;
