import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { produce } from 'immer';
import { toJS } from 'mobx'
import { map } from 'lodash';
import { PlusCircleOutlined, DeleteOutlined } from '@ant-design/icons';
import AggregateFunctionInput from './aggregate-function-input';
import './aggregate-functions.less';

class AggregateFunctionItem extends Component {
  render() {
    const { func, onDelete } = this.props;
    return (
      <div className='aggregate-function-item'>
        <div className='caption'>
          <span className='name'>{ func.name }</span>
          <span className='columns'>({ func.columns.join(", ") })</span>
        </div>
        <div className="actions">
          <DeleteOutlined onClick={onDelete} />
        </div>
        <div className='params'>
          { map(func.params, (value, key) => <div key={key}>{key}: {value}</div>) }
        </div>
      </div>
    )
  }
}

class AggregateFunctions extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showAggregateFunctionInput: true,
      functions: toJS(props.value) || [] // TODO: make sure props.value is mobx observable array
    }
  }

  render() {
    const { groupKey, property, onValueChange } = this.props;

    return (
      <div className='config-group__setting-block'>
        <div className='setting-label'>
          <span>{property.label}</span>
          <PlusCircleOutlined className='plus-icon' onClick={this.showInput} />
        </div>
        <div className='setting-list'>
          { this.state.functions.map((f, i) => <AggregateFunctionItem key={i} func={f} onDelete={this.handleDelete(i)} />)}
        </div>
        {
          this.state.showAggregateFunctionInput ?
            <div className="setting-input-block">
              <AggregateFunctionInput
                groupKey={groupKey}
                property={property}
                onCancel={this.hideInput}
                onConfirm={this.addFunctionAndCloseInput}
                onNew={this.addFunctionOnly}
                onValueChange={onValueChange}
              />
            </div> :
            null
        }
      </div>
    )
  }

  handleDelete = (i) => () => {
    const nextState = produce(this.state, draft => {
      draft.functions.splice(i, 1);
    })
    this._handleValueChange(nextState.functions)
    this.setState(nextState)
  }

  showInput = () => {
    this.setState(
      produce(draft => {
        draft.showAggregateFunctionInput = true
      })
    )
  }

  hideInput = () => {
    this.setState(
      produce(draft => {
        draft.showAggregateFunctionInput = false
      })
    )
  }

  addFunctionAndCloseInput = (func) => {
    const nextState = produce(this.state, draft => {
      draft.functions.push(func);
      draft.showAggregateFunctionInput = false;
    })
    this._handleValueChange(nextState.functions)
    this.setState(nextState)
  }

  addFunctionOnly = (func) => {
    const nextState = produce(this.state, draft => {
      draft.functions.push(func)
    })
    this._handleValueChange(nextState.functions)
    this.setState(nextState)
  }

  _handleValueChange = (functions) => {
    const { groupKey, property, onValueChange } = this.props;
    onValueChange(groupKey, property.key)(functions)
  }
}

AggregateFunctions.propTypes = {
  groupKey: PropTypes.string.isRequired,
  property: PropTypes.object.isRequired,
  onValueChange: PropTypes.func.isRequired
}

export default AggregateFunctions;
