import Ajv from 'ajv';
import transformerSchema from '../schema/transformer.json';
import configSchema from '../schema/config.json';
import mbeanSchema from '../schema/mbean.json';
import magicBeanNodeSchema from '../schema/magicBeanNode.json';
import workflowSchema from '../schema/workflow.json';

const ajv = new Ajv({strict: false});
ajv.addSchema(configSchema);
ajv.addSchema(transformerSchema);
ajv.addSchema(magicBeanNodeSchema);
ajv.addSchema(mbeanSchema);
const validator = ajv.compile(workflowSchema);

export default validator;
