import React, { Component } from "react";
import PropTypes from "prop-types";
import { makeObservable, observable, action } from "mobx";
import { observer } from "mobx-react";
import { Select, Input } from "antd";

import "./index.less";

const SelectOption = Select.Option;

@observer
class NodeDetailsPanel extends Component {
  @observable isCollapsed = false;

  constructor() {
    super();
    makeObservable(this);
  }

  render() {
    let { node } = this.props;

    // TODO: fetch feature sets through API
    const featureSets = [
      { id: "1", name: "events"},
      { id: "2", name: "user_props"}
    ]

    return (
      <div
        className={`node-details-panel ${this.isCollapsed ? "collapsed" : ""}`}
      >
        <div className="node-details-panel__header">
          <div className="node-details-panel__header--node-icon">
            <i className="iconfont icondatabase" />
          </div>

          <div className="node-details-panel__header--node-info">
            <div className="node-details-panel__header--node-name">
              {node.name}
            </div>
            <div className="node-details-panel__header--node-type">
              {node.transformer["class"]}
            </div>
          </div>
        </div>

        <div className="node-details-panel__breif-info">
          <div className="node-details-panel__parent-info">
            <span className="node-details-panel__parent-info--label">
              Parent:
            </span>
            <span className="node-details-panel__parent-info--parents">
              {(node.parent || []).join(", ")}
            </span>
          </div>

          <div className="node-details-panel__desc">{node.comment}</div>

          <div className="node-details-panel__running-btn">Run this node</div>

          <div className="node-details-panel__check-box">
            <input
              type="checkbox"
              id="skip"
              checked={node.skip}
              onChange={this.changeSetting("skip")}
            />
            <label htmlFor="skip">Skip this node from running</label>
          </div>

          <div className="node-details-panel__check-box">
            <input
              type="checkbox"
              id="persistent"
              checked={node.persistent}
              onChange={this.changeSetting("persistent")}
            />
            <label htmlFor="persistent">Persistent</label>
          </div>

          <div className="node-details-panel__accept">
            <div>Accept MBean types</div>
            <Select
              className="accept-input"
              mode="tags"
              value={node.accept}
              onChange={this.changeSetting("accept")}
            ></Select>
          </div>

          {node.transformer.className === "MBeanLoader" ? (
            <div className="node-details-panel__mbean">
              <div>MBean</div>
              <Select className='mbean-select' placeholder="select a feature set">
                { this._renderFeatureSetOptions(featureSets)}
              </Select>
            </div>
          ) : null}
        </div>
      </div>
    );
  }

  @action
  toggleCollapse = () => {
    this.isCollapsed = !this.isCollapsed;
  };

  changeSetting = (key) => (eventOrValue) => {
    if (key === "skip" || key === "persistent") {
      this.props.updateNodeDetailSetting(key, eventOrValue.target.checked);
    } else if (key === "accept") {
      this.props.updateNodeDetailSetting(key, eventOrValue);
    }
  };

  _renderFeatureSetOptions = (featureSets) => {
    return featureSets.map((featureSet, i) => (
      <SelectOption key={i} value={featureSet.id}>{featureSet.name}</SelectOption>
    ))
  }
}

NodeDetailsPanel.propTypes = {
  node: PropTypes.object.isRequired,
  updateNodeDetailSetting: PropTypes.func.isRequired,
};

export default NodeDetailsPanel;
