import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './index.less';

class PublishTemplateModal extends Component {
  render() {
    return (
      <div className="publish_template_modal">
        <div className="publish_template_modal__layer" />

        <div className="publish_template_modal__body">
          <div className="publish_template_modal__body--label">Publish workflow template</div>

          <div className="publish_template_modal__body--form-block">
            <div className="publish_template_modal__body--form-label">Name</div>
            <input className="publish_template_modal__body--form-input" placeholder="Enter name for this template" />
          </div>

          <div className="publish_template_modal__body--form-block">
            <div className="publish_template_modal__body--form-label">Description</div>
            <textarea className="publish_template_modal__body--form-textarea" placeholder="Enter description for this template" />
          </div>

          <div className="publish_template_modal__body--btn-group">
            <div className="publish_template_modal__body--btn cancel" onClick={ this.props.closeModal }>Cancel</div>
            <div className="publish_template_modal__body--btn create" onClick={ this.publishTemplate }>Publish template</div>
          </div>

          <div className="publish_template_modal__body--close-btn" onClick={ this.props.closeModal }>
            <i className="iconfont iconremove" />
          </div>
        </div>
      </div>
    );
  }

  publishTemplate = () => {
    this.props.publishTemplate();
    this.props.navigateTo('/predictions/new');
  }
}

PublishTemplateModal.propTypes = {
  closeModal: PropTypes.func.isRequired,
  publishTemplate: PropTypes.func.isRequired,
  navigateTo: PropTypes.func.isRequired,
};

export default PublishTemplateModal;
