import React, { useState } from "react";
import PropTypes from "prop-types";
import { Select, Button, Form } from "antd";
import { MergeByConditionOperators } from '../../constants';
import "./index.less";

const SelectOption = Select.Option;
const FormItem = Form.Item;

const ConditionForm = (props) => {
  const { columns } = props;
  const [form] = Form.useForm();

  const handleFinish = (values) => {
    props.onFinish(values);
  };

  const initialValues = {
    operator: "==",
  };

  return (
    <Form
      className="condition-form"
      form={form}
      layout="vertical"
      onFinish={handleFinish}
      initialValues={initialValues}
    >
      <FormItem name="leftColumn" rules={[{ required: true }]}>
        <Select placeholder="select a column">
          {columns.map((c, i) => (
            <SelectOption key={i} value={"$" + c.name}>
              {c.name}
            </SelectOption>
          ))}
        </Select>
      </FormItem>
      <FormItem name="operator" rules={[{ required: true }]}>
        <Select placeholder="select a operator">
          {MergeByConditionOperators.map((op, i) => (
            <SelectOption key={i} value={op.key}>
              {op.name}
            </SelectOption>
          ))}
        </Select>
      </FormItem>
      <FormItem name="rightColumn" rules={[{ requried: true }]}>
        <Select name="rightColumn" placeholder="select a column">
          {columns.map((c, i) => (
            <SelectOption key={i} value={"$" + c.name}>
              {c.name}
            </SelectOption>
          ))}
        </Select>
      </FormItem>
      <div className="condition-form__button-group">
        <Button className="cancel-button" onClick={props.onCancel}>
          Cancel
        </Button>
        <Button className="confirm-button" type="primary" htmlType="submit">
          Add
        </Button>
      </div>
    </Form>
  );
};

ConditionForm.propTypes = {
  columns: PropTypes.arrayOf(PropTypes.object).isRequired,
  onCancel: PropTypes.func,
};

export default ConditionForm;
