import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';

import { GraphX, stringifyWithoutCircular } from '../../lib/MxGraphUtils.js';

import { Select } from 'antd';
const { Option } = Select;

import './index.less';

@inject('routingStore', 'templatesStore', 'projectStore')
@observer
class Templates extends Component {
  componentDidMount = () => {
    this.init();
  }

  init = () => {
    this.graphx = new GraphX(document.querySelector('.templates__canvas--container'));
    this.graphx.render(this.props.templatesStore.currentTemplate);
  }

  render() {
    const { templatesStore, projectStore } = this.props;

    return (
      <div className="templates">
        <div className="templates__project-detail-panel">
          <div className="templates__project-detail-panel--panel-label">Project details</div>

          <div className="templates__project-detail-panel--form-label">Project name</div>
          <input className="templates__project-detail-panel--form-input" ref="projectName" placeholder="project name" />

          <div className="templates__project-detail-panel--form-label">Description</div>
          <textarea className="templates__project-detail-panel--form-textarea" ref="projectDesc" placeholder="description..." />

          <div className="templates__project-detail-panel--form-label">Tempaltes</div>
          <Select
            className="templates__project-detail-panel--form-select"
            defaultValue={templatesStore.currentTemplate.id}
            onChange={this.previewTemplate}
            style={{ width: '100%' }}
            placeholder="Select a template"
            size="large"
          >
            {
              templatesStore.templates.map((t) => (
                <Option key={t.id} value={t.id}>{t.name}</Option>
              ))
            }
          </Select>

          <div className="templates__project-detail-panel--btns-group">
            <div className="templates__project-detail-panel--btn cancel" onClick={ this.cancel }>Cancel</div>
            <div className="templates__project-detail-panel--btn create" onClick={ this.create }>Create project</div>
          </div>
        </div>

        <div className="templates__info-container">
          <div className="templates__detail-info">
            <div className="templates__detail-info--label">Template details</div>

            <div className="templates__detail-info--name">Default template</div>

            <div className="templates__detail-info--desc">
              This is a typical template that is created to handle simple prediction tasks.
            </div>

            <div>
              <div className="templates__detail-info--info-block">
                <label>Users segment:</label>
                <span>All</span>
              </div>

              <div className="templates__detail-info--info-block">
                <label>Weeks of training data:</label>
                <span>26</span>
              </div>

              <div className="templates__detail-info--info-block">
                <label>Prediction Label:</label>
                <span>Label_name</span>
              </div>

              <div className="templates__detail-info--info-block">
                <label>Project type:</label>
                <span className="highlight">Binary classification</span>
              </div>

              <div className="templates__detail-info--info-block">
                <label>Created by:</label>
                <span>User #1</span>
              </div>
            </div>
          </div>

          <div className="templates__canvas">
            <div className="templates__canvas--container" />
            <div className="templates__canvas--layer" />
          </div>
        </div>
      </div>
    );
  }

  cancel = () => {
    this.props.routingStore.push('/projects');
  }

  create = () => {
    const { projectName, projectDesc } = this.refs;
    this.props.projectStore.create({
      name: projectName.value,
      description: projectDesc.value,
      projectType: "BinaryClassification",
    }, (project) => {
      this.props.routingStore.push(`/prediction/${project.id}`);
    });

    // this.props.templatesStore.copyTemplate();
  }

  previewTemplate = (templateId) => {
    const template = this.props.templatesStore.templates.find(t => t.id == templateId)
    this.props.templatesStore.previewTemplate(template);
    this.graphx.render(template);
  }
}

export default Templates;
