import { observable, action, makeAutoObservable } from "mobx";
import imUpdate from "immutability-helper";

class ProjectStore {
  @observable projects = [];
  @observable currentProjectId = null;
  @observable graphxClient = null;

  constructor(rulesStore) {
    makeAutoObservable(this);

    this.rulesStore = rulesStore;
    this.bindings = {};
  }

  registerTopics(stompWebsocket, topics) {
    this.stompWebsocket = stompWebsocket;
    topics.forEach((topic) => {
      this.stompWebsocket.registerCallback(topic, (frame) =>
        this.onStompMessage(frame)
      );
    });

    this.fetchAllProjects();
  }

  _setCurrentProjectForRulesStore = (projectId) => {
    this.currentProjectId = projectId;
    this.rulesStore.currentProject = this.projects.find(
      (p) => p.id == projectId
    );
    this.rulesStore.layouts = this.rulesStore.currentProject.layouts
      ? this.rulesStore.currentProject.layouts.map((l) => JSON.parse(l.layout))
      : [];
    this.rulesStore.configs = this.rulesStore.currentProject.configs
      ? this.rulesStore.currentProject.configs.map((c) => JSON.parse(c.config))
      : [];
    let nodesMap = {};
    this.rulesStore.configs.forEach((n) => {
      nodesMap[n.id] = n;
    });
    this.rulesStore.nodesMap = nodesMap;
    this.rulesStore.currentNodeId = null;
  };

  onStompMessage(frame) {
    let payload = JSON.parse(frame.body);
    const { content, status, action, cid: ref } = payload;
    switch (action) {
      case "AllProjects":
        this.projects = content;
        this.invokeCallback(content, ref, status);
        break;
      case "CreateProject": // reply message
        if (status === "Success") {
          this.projects.push(content);
          this.invokeCallback(content, ref, status);
        }
        break;
      case "CreatedProject": // push message
        this.projects.push(content);
        break;
      case "UpdateProject":
        if (status == "Success") {
          const projectIdx = this.projects.map((p) => p.id).indexOf(content.id);
          this.projects = imUpdate(this.projects, {
            $splice: [
              [[projectIdx], 1, { ...this.projects[projectIdx], ...content }],
            ],
          });
          // this._setCurrentProjectForRulesStore(content.id);
          this.invokeCallback(content, ref, status);
        }
        break;
      case "UpdatedProject":
        const projectIdx = this.projects.map((p) => p.id).indexOf(content.id);
        this.projects = imUpdate(this.projects, {
          $splice: [
            [[projectIdx], 1, { ...this.projects[projectIdx], ...content }],
          ],
        });
        this._setCurrentProjectForRulesStore(content.id);

        // update canvas layouts.
        if (content.id == this.currentProjectId) {
          this.graphxClient.render({ layouts: this.rulesStore.layouts });
        }
        break;
      case "DeleteProject":
        if (status == "Success") {
          this.projects = this.projects.filter((p) => p.id != content.id);
          this.invokeCallback(content, ref, status);
        }
        break;
      case "DeletedProject":
        this.projects = this.projects.filter((p) => p.id != content.id);
        break;
    }
  }

  invokeCallback(content, ref, status) {
    if (ref && this.bindings[ref]) {
      this.bindings[ref](content);
      delete this.bindings[ref];
    }
  }

  create = (project, callback) => {
    const ref = this._makeBinding(callback);
    this.stompWebsocket.send(
      "/app/create_project",
      { cid: ref },
      JSON.stringify({ project })
    );
  };

  update = (project, callback) => {
    const ref = this._makeBinding(callback);
    this.stompWebsocket.send(
      "/app/update_project",
      { cid: ref },
      JSON.stringify(project)
    );
  };

  fetchAllProjects = (callback) => {
    const ref = this._makeBinding(callback);
    this.stompWebsocket.send("/app/get_projects", { cid: ref });
  };

  fetchAllProjectsIfNeeded = (callback) => {
    if (this.projects.length === 0) {
      this.fetchAllProjects(callback);
    }
  };

  delete = (id, callback) => {
    const ref = this._makeBinding(callback);
    this.stompWebsocket.send(
      "/app/delete_project",
      { cid: ref },
      JSON.stringify({ id })
    );
  };

  setCurrentProject = (projectId) => {
    if (this.projects.length > 0) {
      this._setCurrentProjectForRulesStore(projectId);
    } else {
      this.fetchAllProjects(() => {
        this._setCurrentProjectForRulesStore(projectId);
      });
    }
  };

  _makeBinding = (callback) => {
    let cb = callback || function () {};
    let ref = this.stompWebsocket.makeRef();
    this.bindings[ref] = cb;
    return ref;
  };

  setGraphXClient = (client) => {
    this.graphxClient = client;
  };
}

export default ProjectStore;
