import React from 'react';
import PropTypes from 'prop-types';
import { Select } from 'antd';

const { Option } = Select;

class MultiSelect extends React.Component {
  render() {
    const { value, groupKey, property, onValueChange } = this.props;

    return (
      <div className="config-group__setting-block">
        <div className="setting-label">
          {property.label}
        </div>

        <div className="setting-input-block">
          <Select
            mode="multiple"
            allowClear
            style={{ width: '100%' }}
            placeholder={property.placeholder}
            value={value}
            onChange={onValueChange(groupKey, property.key)}
          >
            {this._generateSelectOptions(property)}
          </Select>
        </div>
      </div>
    )
  }

  _generateSelectOptions = (property) => {
    return property.selectOptions.map((item, i) => <Option key={i} value={item.key}>{item.name}</Option>)
  }
}

MultiSelect.propTypes = {
  groupKey: PropTypes.string.isRequired,
  property: PropTypes.object.isRequired,
  onValueChange: PropTypes.func.isRequired
}

export default MultiSelect;
