import React, { useState, useRef, useEffect } from "react";
import NODES from "../../constants/Nodes";
import { omitBy } from "lodash";
import { Input } from "antd";
import PropTypes from "prop-types";

import "./index.less";

const NodeListBar = (props) => {
  const [nodes, setNodes] = useState(props.nodes);

  // make react useEffect hook not run on initial render
  // https://stackoverflow.com/questions/53253940/make-react-useeffect-hook-not-run-on-initial-render
  const firstUpdate = useRef(true);

  useEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
    } else {
      window[props.graphxUUID]._initToolbar();
    }
  }, [nodes]);

  const handleKeywordChange = (event) => {
    const keyword = event.target.value;
    if (keyword) {
      const matchedNodes = omitBy(NODES, (node) => {
        return !node.name.toLowerCase().includes(keyword);
      });
      setNodes(matchedNodes);
    } else {
      setNodes(NODES);
    }
  };

  return (
    <div className={`nodes-list-bar ${props.isShowing ? "" : "hidden"}`}>
      <div className="nodes-list-bar__header">Add node</div>

      <div className="nodes-list-bar__nodes-list">
        <div className="nodes-list-bar__nodes-list--title">Toolbox</div>

        <div className="nodes-list-bar__nodes-list--desc">
          To start creating your worklow, drag and drop objects from box below
        </div>
        <div className="nodes-list-bar__nodes-list--search">
          <Input placeholder="Search node" onChange={handleKeywordChange} />
        </div>

        <div
          id="workflow-toolbar"
          className="nodes-list-bar__nodes-list--nodes-block"
        >
          {Object.keys(nodes).map((key) => (
            <div
              className="nodes-list-bar__nodes-list--node"
              data-node-key={key}
              data-node-name={nodes[key].name}
              key={nodes[key].name}
            >
              <i className={`iconfont ${nodes[key].iconClassName}`} />

              <div className="nodes-list-bar__nodes-list--node-info">
                <div className="nodes-list-bar__nodes-list--node-info-title">
                  {nodes[key].name}
                </div>
                <div className="nodes-list-bar__nodes-list--node-info-desc">
                  {nodes[key].description}
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

NodeListBar.propTypes = {
  isShowing: PropTypes.bool.isRequired,
};

export default NodeListBar;
