import React, { useState } from "react";
import PropTypes from "prop-types";
import { Dropdown, Menu } from "antd";
import { UpCircleOutlined, DownCircleOutlined } from "@ant-design/icons";

const Select = (props) => {
  const { groupKey, property, onValueChange, node, group } = props;
  const [collapsed, setCollapsed] = useState(group && !group.required);
  const toggleCollapse = () => {
    setCollapsed(!collapsed);
  };

  const generateSubmenus = (property, node) => {
    let sourceData = [];

    if (!!property.selectOptions) {
      sourceData = property.selectOptions;
      return sourceData.map((option) => (
        <Menu.Item key={option.key} value={option.value}>
          {option.name}
        </Menu.Item>
      ));
    }

    if (
      !!property.optionsSetting &&
      !!property.optionsSetting.readFromNodeAttr
    ) {
      sourceData = node[property.optionsSetting.readFromNodeAttr];

      if (!!property.optionsSetting.transform) {
        // ???
        sourceData = sourceData.map((n) =>
          props[property.optionsSetting.transform](n)
        );
      }
    }

    return sourceData.map((n, i) => (
      <Menu.Item key={n[property.optionsSetting.keyAttr]}>
        {n[property.optionsSetting.displayAttr]}
      </Menu.Item>
    ));
  };

  // render group
  if (group) {
    const menus = (
      <Menu onClick={onValueChange(group.key)}>
        {generateSubmenus(group, node)}
      </Menu>
    );

    const selectedpropertyValue =
      node.transformer.process[group.key] || group.defaultValue;

    const selectedValueLabel = selectedpropertyValue
      ? group.selectOptions.find(
          (item) =>
            item.value === selectedpropertyValue ||
            item.key === selectedpropertyValue
        ).name
      : group.placeholder;

    return (
      <Dropdown overlay={menus} trigger={["click"]} disabled={group.disabled}>
        <div className="config-group__setting-block">
          <div className="setting-label">
            <div className="setting-label__caption">{group.label}</div>
            <div className="setting-label__toggle" onClick={toggleCollapse}>
              {collapsed ? <DownCircleOutlined /> : <UpCircleOutlined />}
            </div>
          </div>

          {collapsed ? null : (
            <div className="setting-input-block">
              <div
                className={`setting-input select ${
                  !!node.transformer.process[group.key] ? "" : "empty"
                }`}
              >
                {selectedValueLabel}
              </div>
              <i className="edit-icon iconfont iconvector-down" />
            </div>
          )}
        </div>
      </Dropdown>
    );
  }

  // otherwise render property of group
  const menus = (
    <Menu onClick={onValueChange(groupKey, property.key)}>
      {generateSubmenus(property, node)}
    </Menu>
  );

  const selectedpropertyValue =
    node.transformer.process[groupKey][property.key] || property.defaultValue;

  const selectedValueLabel = selectedpropertyValue
    ? property.selectOptions.find(
        (item) =>
          item.value === selectedpropertyValue ||
          item.key === selectedpropertyValue
      ).name
    : property.placeholder;

  return (
    <Dropdown overlay={menus} trigger={["click"]} disabled={property.disabled}>
      <div className="config-group__setting-block">
        <div className="setting-label">{property.label}</div>

        <div className="setting-input-block">
          <div
            className={`setting-input select ${
              !!node.transformer.process[groupKey] ? "" : "empty"
            }`}
          >
            {selectedValueLabel}
          </div>
          <i className="edit-icon iconfont iconvector-down" />
        </div>
      </div>
    </Dropdown>
  );
};

Select.propTypes = {
  onValueChange: PropTypes.func.isRequired,
  node: PropTypes.object.isRequired,
  groupKey: PropTypes.string,
  property: PropTypes.object,
  group: PropTypes.object,
};

export default Select;
