import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './index.less';

class RenameColumnSetting extends Component {
  render() {
    let { cancel, config } = this.props;

    return (
      <div className="rename-column-setting">
        <div className="setting-block">
          <div className="setting-label">Table</div>
          <input className="setting-input" placeholder="Select table" ref="table" defaultValue={ config.table } />
        </div>

        <div className="setting-block">
          <div className="setting-label">Column</div>
          <input className="setting-input with-bottom-margin" placeholder="Select column" ref="column" defaultValue={ config.column } />
          <input className="setting-input" placeholder="New name" ref="newName" defaultValue={ config.newName } />
        </div>

        <div className="setting-block">
          <input className="setting-check-box" type="checkbox" />
          <label className="setting-check-box-label">continue adding</label>
        </div>

        <div className="setting-btns">
          <div className="setting-btns__cancel" onClick={ cancel }>Cancel</div>
          <div className="setting-btns__add" onClick={ this.added }>Add</div>
        </div>
      </div>
    );
  }

  added = () => {
    let data = {
      table: this.refs["table"].value,
      column: this.refs["column"].value,
      newName: this.refs["newName"].value,
    };

    this.props.added("rename_column")(data);
  }
}

RenameColumnSetting.propTypes = {
  cancel: PropTypes.func.isRequired,
  added: PropTypes.func.isRequired,
  config: PropTypes.object.isRequired,
};

export default RenameColumnSetting;
