import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import update from 'immutability-helper';

import { makeObservable, observable, action, computed } from 'mobx';
import { observer } from 'mobx-react';

import ConfigSettingPanel from '../ConfigSettings';
import SavedConfigsList from '../SavedConfigsList';

import './index.less';

@observer
class AfterConfigPanel extends Component {
  @observable isShowingPanel = false;
  @observable currentConfigInfo = null;

  constructor() {
    super();
    makeObservable(this);
  }

  render() {
    let { config } = this.props;

    return (
      <div className="before-config-panel">
        <div className="before-config-panel__desc">
          Below are the operations that are applied on columns, you can rename, add new column, skip, or filter them.
        </div>

        <div className="before-config-panel__body">
          {
            this.isShowingPanel || this.currentConfigInfo ?
              <ConfigSettingPanel
                config={ this.currentConfig }
                configType={ this.currentConfigInfo ? this.currentConfigInfo.type : null }
                cancel={ this.closeConfigPanel }
                added={ this.addedNewRule }
              />:
              <Fragment>
                <div className="before-config-panel__add-operation-btn" onClick={ this.openConfigPanel } />
                <SavedConfigsList
                  configs={ config }
                  setCurrentConfigInfo={ this.setCurrentConfigInfo }
                  removeRule={ this.removeRule }
                />
              </Fragment>
          }
        </div>
      </div>
    );
  }

  openConfigPanel = () => {
    this.isShowingPanel = true;
  }

  closeConfigPanel = () => {
    this.isShowingPanel = false;
    this.currentConfigInfo = null;
  }

  addedNewRule = (type) => (rule) => {
    this.isShowingPanel = false;
    this.currentConfigInfo = null;

    let config = {...this.props.config};
    if (!!config[type]) {
      config[type].push(rule);
    } else {
      config[type] = [rule];
    }
    this.props.updateNodeTransformerSetting("after", config);
  }

  removeRule = (type, idx) => {
    this.isShowingPanel = false;
    this.currentConfigInfo = null;

    const config = update(this.props.config, {
      [type]: {
        $splice: [[idx, 1]]
      }
    });
    this.props.updateNodeTransformerSetting("after", config);
  }

  setCurrentConfigInfo = (type, idx) => {
    this.currentConfigInfo = { type, idx };
  }

  @computed
  get currentConfig() {
    if (!this.currentConfigInfo) {
      return {};
    }

    const { type, idx } = this.currentConfigInfo;
    return this.props.config[type][idx];
  }
}

AfterConfigPanel.propTypes = {
  config: PropTypes.object.isRequired,
  updateNodeTransformerSetting: PropTypes.func.isRequired
};

export default AfterConfigPanel;
