import mx from 'mxgraph';
import { v1 as uuidv1 } from 'uuid';

const { mxObjectCodec, mxUtils, mxClient, mxEvent, mxGraph, mxRubberband, mxConstants, mxPerimeter, mxEdgeStyle, mxToolbar, mxCell, mxGeometry, mxKeyHandler } = mx();

export class MxGraph2Json extends mxObjectCodec {
  constructor() {
    super( (value) => {} );
  }

  encode(value) {
    const xmlDoc = mxUtils.createXmlDocument();
    const newObject = xmlDoc.createElement("Object");
    for(let prop in value) {
      newObject.setAttribute(prop, value[prop]);
    }
    return newObject;
  }

  decode(model) {
    return Object.keys(model.cells).map(
      (iCell)=>{
        const currentCell = model.getCell(iCell);
        return (currentCell.value !== undefined)? currentCell : null;
      }
    ).filter((item)=> (item !== null));
  }
}

export class GraphX {
  constructor(container, toolbar, addNewNode, updateLayouts, connectNodes, selectNode, removeNodes){
    if (!mxClient.isBrowserSupported()) {
      return mxUtils.error('Browser is not supported!', 200, false);
    }
    mxEvent.disableContextMenu(container);

    this._graph = new mxGraph(container);
    this._graph.setConnectable(true);
    this._graph.setAllowDanglingEdges(false);
    // new mxRubberband(this._graph); // Enables rubberband selection

    this.onDroppedCallback = (key, id, name) => {
      addNewNode(key, id, name);
      updateLayouts(this.getJsonModel());
    };
    this.onConnected = (sourceId, targetId) => {
      connectNodes(sourceId, targetId);
      updateLayouts(this.getJsonModel());
    }
    this.onSelectNode = selectNode;
    this.onCellsRemoved = (cells) => {
      const cellIds = cells.map((cell) => cell.id);

      removeNodes(cells);
      updateLayouts(this.getJsonModel().filter((cell) => cellIds.indexOf(cell.id) < 0));
    }
    this._toolbar = new mxToolbar(toolbar);
    this._toolbar.enabled = false;

    this._initToolbar();
    this._initConnectionHandler();
    this._initSelectionCellsHandler();
    this._initCellsRemovedHandler();
    this._initDeleteKeyHandler();

    this.labelDisplayOveride();
    this.styling();
  }

  labelDisplayOveride() { // Overrides method to provide a cell label in the display
    this._graph.convertValueToString = (cell)=> {
      if (mxUtils.isNode(cell.value)) {
        if (cell.value.nodeName.toLowerCase() === 'object') {
          const name = cell.getAttribute('name', '');
          return name;
        }
      }

      if (cell.isVertex()) {
        if (!!cell.value) {
          return cell.value.name || cell.value;
        }
      }
      return '';
    };
  }

  styling() {
    // Creates the default style for vertices
    let style = [];
    style[mxConstants.STYLE_SHAPE] = mxConstants.SHAPE_RECTANGLE;
    style[mxConstants.STYLE_PERIMETER] = mxPerimeter.RectanglePerimeter;
    style[mxConstants.STYLE_STROKECOLOR] = 'gray';
    style[mxConstants.STYLE_ROUNDED] = true;
    style[mxConstants.STYLE_FILLCOLOR] = '#EEEEEE';
    style[mxConstants.STYLE_GRADIENTCOLOR] = 'white';
    style[mxConstants.STYLE_FONTCOLOR] = 'black';
    style[mxConstants.STYLE_ALIGN] = mxConstants.ALIGN_CENTER;
    style[mxConstants.STYLE_VERTICAL_ALIGN] = mxConstants.ALIGN_MIDDLE;
    style[mxConstants.STYLE_FONTSIZE] = '12';
    style[mxConstants.STYLE_FONTSTYLE] = 1;
    this._graph.getStylesheet().putDefaultVertexStyle(style);

    // Creates the default style for edges
    style = this._graph.getStylesheet().getDefaultEdgeStyle();
    style[mxConstants.STYLE_STROKECOLOR] = '#0C0C0C';
    style[mxConstants.STYLE_LABEL_BACKGROUNDCOLOR] = 'white';
    style[mxConstants.STYLE_EDGE] = mxEdgeStyle.ElbowConnector;
    style[mxConstants.STYLE_ROUNDED] = true;
    style[mxConstants.STYLE_FONTCOLOR] = 'black';
    style[mxConstants.STYLE_FONTSIZE] = '10';
    this._graph.getStylesheet().putDefaultEdgeStyle(style);
  }

  getJsonModel(){
    const encoder = new MxGraph2Json();
    const jsonModel = encoder.decode(this._graph.getModel());
    return jsonModel;
  }

  render(dataModel) {
    // clear the old graph first.
    this._graph.getModel().clear();

    const jsonEncoder = new MxGraph2Json();

    this._vertices = {};
    this._dataModel = dataModel;

    const parent = this._graph.getDefaultParent();
    this._graph.getModel().beginUpdate(); // Adds cells to the model in a single step
    try {

      [
        ...this._dataModel.layouts.filter((c) => !c.edge),
        ...this._dataModel.layouts.filter((c) => !!c.edge)
      ].map(
        (node)=> {
          if(node.value) {
            if(typeof node.value === "object" || !!node.vertex) {
              // const xmlNode = jsonEncoder.encode(node.value);
              let vertex = this._graph.insertVertex(parent, null, node.value, node.geometry.x, node.geometry.y, node.geometry.width, node.geometry.height);
              vertex.setId(node.id);
              vertex.setValue(node.value);
              this._vertices[node.id] = vertex;
            } else if(node.value === "Edge") {
              let vertex = this._graph.insertEdge(parent, null, 'Edge', this._vertices[node.source], this._vertices[node.target])
              vertex.setId(node.id);
              this._vertices[node.id] = vertex;
            }
          }
        }
      );

    } catch (e) {
      console.error(e);
    } finally {
      this._graph.getModel().endUpdate(); // Updates the display
    }
  }

  addVertex(node, icon, w, h, style) {
    let vertex = new mxCell(null, new mxGeometry(0, 0, w, h), style);
    vertex.setVertex(true);

    let img = this._addToolbarItem(this._graph, toolbar, node, vertex, icon, this.onDroppedCallback);
    img.enabled = true;

    this._graph.getSelectionModel().addListener(mxEvent.CHANGE, (evt) => {
      let tmp = this._graph.isSelectionEmpty();
      mxUtils.setOpacity(img, (tmp) ? 100 : 20);
      img.enabled = tmp;

      if (!!tmp) {
        this.onSelectNode(null);
      }
    });
  }

  _initToolbar() {
    document.querySelectorAll(".nodes-list-bar__nodes-list--node").forEach((node) => {
      this.addVertex(node, 'editors/images/rounded.gif', 100, 40, 'shape=rounded');
    });
  }

  _addToolbarItem(graph, toolbar, node, prototype, image, onDroppedCallback) {
    // Function that is executed when the image is dropped on
    // the graph. The cell argument points to the cell under
    // the mousepointer if there is one.
    let onDropped = function(graph, evt, cell, x, y) {
      graph.stopEditing(false);

      let vertex = graph.getModel().cloneCell(prototype);
      vertex.geometry.x = x;
      vertex.geometry.y = y;

      let name = this.element.getAttribute("data-node-name");
      let key = this.element.getAttribute("data-node-key");
      // const jsonEncoder = new MxGraph2Json();
      // vertex.setValue(jsonEncoder.encode({name: name}));
      vertex.setValue({ name });
      vertex.setId(uuidv1());
      vertex.setAttribute("type", key);

      graph.addCell(vertex);
      graph.setSelectionCell(vertex);
      onDroppedCallback(key, vertex.id, name);
    };

    // This listener is always called first before any other listener
    // in all browsers.
    mxEvent.addListener(node, 'mousedown', function(evt) {
      if (node.enabled == false)
        {
          mxEvent.consume(evt);
        }
    });

    mxUtils.makeDraggable(node, graph, onDropped);

    return node;
  }

  _initConnectionHandler() {
    this._graph.connectionHandler.addListener(mxEvent.CONNECT, (sender, evt) => {
      let edge = evt.getProperty('cell');
      let source = this._graph.getModel().getTerminal(edge, true);
      let target = this._graph.getModel().getTerminal(edge, false);
      edge.setId(uuidv1());
      edge.setValue("Edge");

      this.onConnected(source.id, target.id);
    });
  }

  _initSelectionCellsHandler() {
    this._graph.selectionCellsHandler.addListener(mxEvent.ADD, (sender, evt) => {
      let selectedCells = sender.getHandledSelectionCells();
      let cell = selectedCells[0];
      if (cell.isEdge()) {
        return;
      }
      this.onSelectNode(cell.id);
    });
  }

  _initCellsRemovedHandler() {
    const originCellsRemoved = this._graph.cellsRemoved.bind(this._graph);
    this._graph.cellsRemoved = (cells) => {
      this.onCellsRemoved(cells);
      originCellsRemoved(cells);
    }
  }

  _initDeleteKeyHandler() {
    const keyHandler = new mxKeyHandler(this._graph);
    keyHandler.bindKey(46, (evt) => {
      if (this._graph.isEnabled())
        {
          this._graph.removeCells();
        }
    });
    keyHandler.bindKey(8, (evt) => {
      if (this._graph.isEnabled())
        {
          this._graph.removeCells();
        }
    });
  }
}

