import React from "react";
import { MoreOutlined, StarFilled } from "@ant-design/icons";
import { Dropdown, Menu } from "antd";

const MenuItem = Menu.Item;

const Person = ({ data }) => {
  const menu = (
    <Menu>
      <MenuItem>Disable</MenuItem>
    </Menu>
  );

  const dropdown = (
    <Dropdown overlay={menu} trigger={["click"]}>
      <MoreOutlined />
    </Dropdown>
  );

  return (
    <div className="person">
      <div className="person__admin-indicator">
        {data.admin ? <StarFilled /> : null}
      </div>
      <div className="person__avatar-wrapper">
        <img
          src={data.avatarUrl || "https://i.pravatar.cc/32"}
          alt={data.name}
        />
      </div>
      <div className="person__body">
        <div className="name">{data.name}</div>
        <div className="role">{data.role}</div>
        <div className="joined-at">{data.joinedAt}</div>
      </div>
      <div className="action">{dropdown}</div>
    </div>
  );
};

export default Person;
