import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { produce } from 'immer';
import { Input, Select, Button } from 'antd';
import './aggregate-functions.less';

const InputGroup = Input.Group;
const SelectOption = Select.Option;

const AvailableAggregateFunctions = [
  { name: "count", label: "Count" },
  { name: "sum", label: "Sum" },
  { name: "min", label: "Min" },
  { name: "max", label: "Max" },
  { name: "avg", label: "Avg" },
  { name: "mean", label: "Mean" },
  { name: "decay", label: "Decay" },
  { name: "unique", label: "Unique" },
  { name: "entropy", label: "Entropy" },
  { name: "frequency", label: "Frequency" },
  { name: "topValues", label: "Top values" },
  { name: "topCounts", label: "Top counts" },
  { name: "topPercents", label: "Top percents" },
  { name: "bottomValues", label: "Bottom values" },
  { name: "bottomCounts", label: "Bottom counts" },
  { name: "bottomPercents", label: "Bottom percents" }
]

class AggregateFunctionInput extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: undefined,
      columns: [],
      params: {}
    }
  }

  render() {
    const { groupKey, propery } = this.props;
    const aggFunctionOptions = AvailableAggregateFunctions.map((f, i) => {
      return <SelectOption key={i} value={f.name}>{f.label}</SelectOption>
    })

    let paramsSection = this._generateParamsSection();

    return (
      <div className='aggregate-function-input'>
        <div className="aggregate-function-input__name">
          <Select
            placeholder="select an aggreate function"
            onChange={this.handleFunctionNameChange}
            value={this.state.name}
          >
            {aggFunctionOptions}
          </Select>
        </div>
        <div className="aggregate-function-input__columns">
          <Select
            mode="tags"
            className='aggregate-function-input__columns'
            placeholder="select columns"
            onChange={this.handleColumsChange}
            value={this.state.columns}
          >
          </Select>
        </div>
        { paramsSection }
        <div className="aggregate-function-input__actions">
          <Button
            className='cancel-btn'
            onClick={this.props.onCancel}
          >
            Cancel
          </Button>
          <Button
            className='confirm-btn'
            type='primary'
            onClick={this.handleConfirm}
          >
            Add
          </Button>
          <Button
            className='confirm-and-add-btn'
            type='primary'
            onClick={this.confirmAndResetInput}
          >
            Add another one
          </Button>
        </div>
      </div>
    )
  }

  handleFunctionNameChange = (value) => {
    this.setState({...this.state, name: value });
  }

  handleColumsChange = (value) => {
    this.setState({ ...this.state, columns: value})
  }

  handleDecayIntervalColumnChange = (event) => {
    this.setState({
      ...this.state,
      params: {
        ...this.state.params,
        decayIntervalColumn: event.target.value
      }
    })
  }

  handleDecayRatioChange = (event) => {
    this.setState({
      ...this.state,
      params: {
        ...this.state.params,
        decayRatio: event.target.value
      }
    })
  }

  handleLimitChange = (event) => {
    this.setState(
      produce(draft => {
        draft.params.limit = event.target.value
      })
    )
  }

  handleConfirm = () => {
    this.props.onConfirm(this.state)
  }

  confirmAndResetInput = () => {
    let func = Object.assign({}, this.state);
    this.props.onNew(func);
    this.setState({
      name: undefined,
      columns: [],
      params: {}
    });
  }

  _generateParamsSection = () => {
    let section = null;
    switch (this.state.name) {
      case 'decay':
        // decayInterval(string) and decayRatio(number) is required
        section = (
          <div className='aggregate-function-input__params'>
            <InputGroup className='params-group' compact>
              <Input
                className='decay-interval-column'
                placeholder='Decay interval column'
                onChange={this.handleDecayIntervalColumnChange}
              />
              <Input
                className='decay-ratio'
                type='number'
                placeholder='Decay ratio'
                onChange={this.handleDecayRatioChange}
              />
            </InputGroup>
          </div>
        )
        break;

      case 'topValues':
      case 'topCounts':
      case 'topPercents':
      case 'bottomValues':
      case 'bottomCounts':
      case 'bottomPercents':
        // limit(number) is required
        section = (
          <div className='aggregate-function-input__params'>
            <Input
              type='number'
              placeholder='limit'
              onChange={this.handleLimitChange}
            />
          </div>
        )
        break;

      default:
        break;
    }

    return section;
  }
}

AggregateFunctionInput.propTyps = {
  groupKey: PropTypes.string.isRequired,
  property: PropTypes.object.isRequired,
  onValueChange: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired,
  onConfirm: PropTypes.func.isRequired
}

export default AggregateFunctionInput;
