import React, { Component } from "react";
import { inject, observer } from "mobx-react";

import moment from "moment";

import "./index.less";

@inject("routingStore", "projectStore")
@observer
class Projects extends Component {
  render() {
    const { projects } = this.props.projectStore;

    return (
      <div className="projects">
        <div className="projects__toolbar">
          <input
            className="projects__toolbar--search-column"
            placeholder="Search"
          />
          <div
            className="projects__toolbar--create-btn"
            onClick={this.newProject}
          >
            Create Project
          </div>
        </div>

        <table className="projects__list">
          <colgroup>
            <col style={{ width: "16%", minWidth: "16%" }} />
          </colgroup>
          <thead>
            <tr>
              <th>Title</th>
              <th>Status</th>
              <th>Last run</th>
              <th>Creation date</th>
              <th>Owner</th>
            </tr>
          </thead>

          <tbody>
            {!projects || projects.length == 0 ? (
              <tr className="empty">
                <div className="empty-placeholder-block">
                  <div className="create-link" onClick={this.newProject}>
                    Create Project
                  </div>
                  <div className="empty-placeholder">
                    You dont have any project yet. start your prediction
                    project
                  </div>
                </div>
              </tr>
            ) : null}

            {projects.map((p) => (
              <tr
                className="projects__list--item"
                key={p.id}
                onClick={this.navigateToProjectDetailPage(p.id)}
              >
                <th>
                  <div className="projects__list--project-name">
                    {p.project.name}
                  </div>

                  <div className="projects__list--project-desc">
                    {p.project.description}
                  </div>
                </th>

                <th>
                  <div className="projects__list--status-bar-bg">
                    <div
                      className="projects__list--status-bar-process processing"
                      style={{ width: "56%" }}
                    />
                  </div>

                  <div className="projects__list--status-bar-info">
                    Running (1h 11min)
                  </div>
                </th>

                <th>
                  <div className="projects__list--date">
                    {moment(p.project.lastRun).format("MM-DD-YYYY")}
                  </div>
                  <div className="projects__list--time">
                    {moment(p.project.lastRun).format("hh:mm")}
                  </div>
                </th>

                <th>
                  <div className="projects__list--date">
                    {moment(p.project.createdTime).format("MM-DD-YYYY")}
                  </div>
                  <div className="projects__list--time">
                    {moment(p.project.createdTime).format("hh:mm")}
                  </div>
                </th>

                <th>
                  <div className="projects__list--owner-avatar" />
                  <div className="projects__list--owner-name">
                    {p.project.creatorName}
                  </div>
                </th>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  }

  newProject = () => {
    this.props.routingStore.push("/predictions/new");
  };

  navigateToProjectDetailPage = (projectId) => () => {
    this.props.routingStore.push(`/prediction/${projectId}`);
  };
}

export default Projects;
