import React,  { useState } from "react";
import { Input } from "antd";
import { range } from "lodash";
import { produce } from 'immer';
import PropTypes from "prop-types";
import "./fixed-array-input.less";

const FixedArrayInput = (props) => {
  const { groupKey, property, onValueChange, value } = props;
  const [ arrayValue, setArrayValue ] = useState(property.defaultValue);

  const handleValueChange = (i) => (event) => {
    const updatedArrayValue = produce(arrayValue, draft => {
        draft[i] = event.target.value
      })
    setArrayValue(updatedArrayValue)
    onValueChange(groupKey, property.key)(updatedArrayValue)
  }

  return (
    <div className="process-config-panel__setting-block">
      <div className="setting-label">{property.label}</div>

      <div className="setting-input-block fixed-array-input">
        {range(0, property.length).map((i) => (
          <Input
            key={i}
            type={property.items.type}
            value={value ? value[i]: undefined}
            defaultValue={property.defaultValue[i]}
            onChange={handleValueChange(i)}
          />
        ))}
      </div>
    </div>
  );
};

FixedArrayInput.propTypes = {
  groupKey: PropTypes.string,
  property: PropTypes.object.isRequired,
  onValueChange: PropTypes.func.isRequired,
  value: PropTypes.array
};

export default FixedArrayInput;
