import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { debounce } from 'lodash';

import { Menu, Dropdown } from 'antd';

import LogsTimeline from '../../components/LogsTimeline';

import './index.less';

class PredictionDetailsPanel extends Component {
  render() {
    const { project } = this.props;

    return (
      <div className="prediction-details-panel">
        <div className="prediction-details-panel__header">Prediction details</div>
        <div className="prediction-details-panel__main">
          <div className="prediction-details-panel__form-block">
            <div className="prediction-details-panel__form-block--label">Prediction name</div>
            <input className="prediction-details-panel__form-block--form-input" defaultValue={ project.project ? project.project.name : "" } onChange={ this.updateProject('name') } />
          </div>

          <div className="prediction-details-panel__form-block">
            <div className="prediction-details-panel__form-block--label">Prediction name</div>
            <textarea className="prediction-details-panel__form-block--form-textarea" defaultValue={ project.project ? project.project.description : "" } onChange={ this.updateProject('description') } />
          </div>

          <div className="prediction-details-panel__inline-info">
            <span className="prediction-details-panel__inline-info--label">Prediction type: </span>
            <span className="prediction-details-panel__inline-info--value">{ project.project ? project.project.projectType : "" }</span>
          </div>

          <Dropdown overlay={ this._renderSettingBtns() }  trigger={['click']}>
            <div className="prediction-details-panel__main--setting-btn" />
          </Dropdown>
        </div>

        <div className="detail-setting-bar__body--gap" />
        <LogsTimeline />
      </div>
    );
  }

  _renderSettingBtns = () => (
    <Menu onClick={ this._reactSetting }>
      <Menu.Item key="edit">
        Edit
      </Menu.Item>
      <Menu.Item key="delete">
        Delete
      </Menu.Item>
    </Menu>
  )

  _reactSetting = (e) => {
    switch (e.key) {
      case "delete": {
        this.props.delete(this.props.project.id, () => {
          this.props.navigateTo('/projects');
        });
        break;
      }
      case "edit": {
        this.props.editProjectDetail(false);
        break;
      }
    }
  }

  updateProject = (column) => {
    return debounce((e) => {
      const project = { ...this.props.project.project, [column]: e.target.value };
      this.props.update({ id: this.props.project.id, project });
    }, 1000);
  }
}

PredictionDetailsPanel.propTypes = {
  project: PropTypes.object.isRequired,
  update: PropTypes.func.isRequired,
  delete: PropTypes.func.isRequired,
  navigateTo: PropTypes.func.isRequired,
  editProjectDetail: PropTypes.func.isRequired,
};

export default PredictionDetailsPanel;
