import React from "react";
import axios from "axios";
import { inject, observer } from "mobx-react";
import { Link, NavLink } from "react-router-dom";
import "./index.less";

const AppHeader = (props) => {
  const handleSignOut = () => {
    axios.get("/api/user/logout").then((res) => {
      props.userStore.signOut();
      props.userStore.stompWebsocket.disconnect();
      props.routingStore.push("/login");
    });
  };

  return (
    <header className="modou-app__header">
      <Link className="logo" to="/">
        <span className="logo__magic">magic</span>
        <span className="logo__bean">bean</span>
        <span className="logo__dot">.</span>
        <span className="logo__ai">ai</span>
      </Link>
      <div className="navbar">
        <NavLink activeClassName="current" to="/projects">
          Predict
        </NavLink>
        <NavLink activeClassName="current" to="/segments">
          Segments
        </NavLink>
        <NavLink activeClassName="current" to="/feature_sets">
          Feature Sets
        </NavLink>
        <NavLink activeClassName="current" to="/admin">
          Admin
        </NavLink>
      </div>
      <div className="sign-out" onClick={handleSignOut}>
        logout
      </div>
      <div className="profile">
        <Link to="/profile">
          <img src="https://i.pravatar.cc/32?img=58" alt="" />
        </Link>
      </div>
    </header>
  );
};

AppHeader.propTypes = {};

export default inject("userStore", "routingStore")(observer(AppHeader));
