import { observable, action, computed, makeObservable } from "mobx";
import axios from "axios";

export default class UserStore {
  @observable userProfile = {
    role: "business_user",
  };

  @observable loading = true;

  @observable authed = false;

  constructor() {
    makeObservable(this);
  }

  test_authentication() {
    return axios
      .get("/stomp/info")
      .then((response) => {
        this.signIn();
      })
      .catch((error) => {
        if (error.response.status === 403) {
          this.signOut();
        }
      });
  }

  registerTopics(stompWebsocket, topics) {
    this.stompWebsocket = stompWebsocket;
    topics.forEach((topic) => {
      this.stompWebsocket.registerCallback(topic, (frame) =>
        this.onStompMessage(frame)
      );
    });
  }

  onStompMessage = (message) => {};

  @action
  signIn() {
    localStorage.setItem("authed", true);
    this.loading = false;
    this.authed = true;
  }

  @action
  signOut() {
    localStorage.removeItem("authed");
    this.loading = false;
    this.authed = false;
  }
}
