import React, { useState } from "react";
import PropTypes from "prop-types";
import { UpCircleOutlined, DownCircleOutlined } from "@ant-design/icons";

const Input = (props) => {
  const { groupKey, group, property, onValueChange, value } = props;
  const [collapsed, setCollapsed] = useState(group && !group.required);

  const toggleCollapse = () => {
    setCollapsed(!collapsed);
  };

  // if property and groupKey are defined, we are going to update the property under the group
  if (property && groupKey) {
    return (
      <div className="config-group__setting-block">
        <div className="setting-label">{property.label}</div>

        <div className="setting-input-block">
          <input
            className="setting-input"
            type={property.dataType}
            min={property.min}
            max={property.max}
            placeholder={property.placeholder}
            onChange={onValueChange(groupKey, property.key)}
            value={value}
            defaultValue={property.defaultValue}
          />
          <i className="edit-icon iconfont iconpencil" />
        </div>
      </div>
    );
  }

  // otherwise we are going to up the group directly
  return (
    <div className="config-group__setting-block">
      <div className="setting-label">
        <div className="setting-label__caption">{group.label}</div>
        <div className="setting-label__toggle" onClick={toggleCollapse}>
          {collapsed ? <DownCircleOutlined /> : <UpCircleOutlined />}
        </div>
      </div>

      {collapsed ? null : (
        <div className="setting-input-block">
          <input
            className="setting-input"
            type={group.dataType}
            min={group.min}
            max={group.max}
            placeholder={group.placeholder}
            value={value}
            onChange={onValueChange(group.key)}
            defaultValue={group.defaultValue}
          />
          <i className="edit-icon iconfont iconpencil" />
        </div>
      )}
    </div>
  );
};

Input.propTypes = {
  groupKey: PropTypes.string,
  group: PropTypes.object,
  property: PropTypes.object,
  onValueChange: PropTypes.func.isRequired,
  value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

export default Input;
