import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { makeObservable, observable, action, computed } from 'mobx';
import { observer } from 'mobx-react';

import { Menu, Dropdown } from 'antd';

import './index.less';

const OPERATIONS = {
  "equal_to": "Equal to",
  "contains": "Contains",
  "not_in": "Not in",
  "is_null": "Is null",
  "is_not_null": "Is not null",
};

@observer
class FilterColumnSetting extends Component {
  @observable currentConfig = {
    operator: "equal_to",
  };

  constructor() {
    super();
    makeObservable(this);
  }

  render() {
    let { cancel, config } = this.props;

    return (
      <div className="filter-column-setting">
        <div className="setting-block">
          <div className="setting-label">Table</div>
          <input className="setting-input" placeholder="Select table" ref="table" defaultValue={ config.table } />
        </div>

        <div className="setting-block">
          <div className="setting-label">Column</div>
          <input className="setting-input with-bottom-margin" placeholder="Set column" ref="column" defaultValue={ config.column } />

          <Dropdown overlay={ this._renderOperations() }  trigger={['click']}>
            <div className="setting-select with-bottom-margin">
              <span>{ config.operator || this.currentOperator }</span>
              <i className="iconfont iconvector-down" />
            </div>
          </Dropdown>

          {
            ["is_null", "is_not_null"].indexOf(this.currentConfig.operator) != -1 ?
              null:
              <input className="setting-input" placeholder="Value" ref="filterValue" defaultValue={ config.filterValue } />
          }
        </div>

        <div className="setting-btns">
          <div className="setting-btns__cancel" onClick={ cancel }>Cancel</div>
          <div className="setting-btns__add" onClick={ this.added }>Add</div>
        </div>
      </div>
    );
  }

  _renderOperations = () => (
    <Menu onClick={this.selectOperation}>
      <Menu.Item key="equal_to">
        { OPERATIONS["equal_to"] }
      </Menu.Item>
      <Menu.Item key="contains">
        { OPERATIONS["contains"] }
      </Menu.Item>
      <Menu.Item key="not_in">
        { OPERATIONS["not_in"] }
      </Menu.Item>
      <Menu.Item key="is_null">
        { OPERATIONS["is_null"] }
      </Menu.Item>
      <Menu.Item key="is_not_null">
        { OPERATIONS["is_not_null"] }
      </Menu.Item>
    </Menu>
  )

  selectOperation = (e) => {
    this.currentConfig.operator = e.key;
  }

  @computed
  get currentOperator() {
    return OPERATIONS[this.currentConfig.operator];
  }

  added = () => {
    let filterValue = ["is_null", "is_not_null"].indexOf(this.currentConfig.operator) != -1 ? undefined : this.refs["filterValue"].value;

    let data = {
      ...this.currentConfig,
      table: this.refs["table"].value,
      column: this.refs["column"].value,
      filterValue: filterValue,
    };

    this.props.added("filter_column")(data);
  }
}

FilterColumnSetting.propTypes = {
  cancel: PropTypes.func.isRequired,
  added: PropTypes.func.isRequired,
  config: PropTypes.object.isRequired,
};

export default FilterColumnSetting;
