import { observable, action, computed, makeObservable } from 'mobx';
import { v1 as uuidv1 } from 'uuid';

import NODES from '../constants/Nodes';
import { stringifyWithoutCircular } from '../lib/MxGraphUtils';

export default class RulesStore {
  @observable currentProject = {};
  @observable nodesMap = {};
  @observable configs = [];
  @observable currentNodeId = null;
  @observable layouts = [];

  constructor() {
    makeObservable(this)
  }

  registerTopics(stompWebsocket) {
    this.stompWebsocket = stompWebsocket;
  }

  @action
  addNewNode = (key, id, name) => {
    let node = { ...NODES[key].defaultTemplate, id, name };

    this.nodesMap[id] = node;
    this.configs.push(this.nodesMap[id]);

    this._updateProject();
  }

  @action
  selectNode = (id) => {
    this.currentNodeId = id;
  }

  @action
  connectNodes = (parentId, selfId) => {
    let node = this.nodesMap[selfId];
    node.parent = node.parent.concat([parentId]);

    this._updateProject();
  }

  @action
  removeNodes = (nodes) => {
    nodes.forEach((node) => {
      if (node.isEdge()) {
        let childNode = this.nodesMap[node.target.id];
        if (!!childNode) {
          childNode.parent = childNode.parent.filter((p) => p != node.source.id);
        }
      } else {
        this.configs = this.configs.filter((n) => n.id != node.id);
      }
    });
  }

  @action
  updateNodeTransformerSetting = (excuteAt, configs) => {
    let node = this.getCurrentNode;
    node["transformer"][excuteAt] = configs;

    this._updateProject();
  }

  @action
  updateNodeDetailSetting = (key, value) => {
    let node = this.getCurrentNode;
    node[key] = value;

    this._updateProject();
  }

  @action
  updateLayouts = (modelJson) => {
    this.layouts = JSON.parse(stringifyWithoutCircular(modelJson));

    this._updateProject();
  }

  @action
  applyTemplate = (configs, layouts) => {
    let uuidsMapping = {};
    layouts.forEach((cell) => {
      uuidsMapping[cell.id] = uuidv1();
    });

    this.configs = configs.map((rule) => {
      return {
        ...rule,
        id: uuidsMapping[rule.id],
        parent: rule.parent.map(p => uuidsMapping[p])
      };
    });

    this.layouts = layouts.map((layout) => {
      let newLayout = {
        ...layout,
        id: uuidsMapping[layout.id],
        source: uuidsMapping[layout.source] || null,
        target: uuidsMapping[layout.target] || null,
      }

      if (!!layout.edges) {
        newLayout = {
          ...newLayout,
          edges: layout.edges.map(edge => (
            {
              ...edge,
              id: uuidsMapping[edge.id],
              source: uuidsMapping[edge.source],
              target: uuidsMapping[edge.target]
            }
          ))
        }
      };

      return newLayout;
    });

    this.configs.forEach(r => this.nodesMap[r.id] = r);
  }

  findNodeById = (id) => {
    return this.nodesMap[id];
  }

  @computed
  get getCurrentNode() {
    return this.nodesMap[this.currentNodeId];
  }

  _updateProject = () => {
    if (!!this.updateDebounce) {
      clearTimeout(this.updateDebounce);
    }
    this.updateDebounce = setTimeout(() => {
      this.stompWebsocket.send('/app/update_project', {}, JSON.stringify({
        id: this.currentProject.id,
        configs: this.configs.map((c) => {
          return {
            id: c.id,
            name: c.name,
            description: c.description,
            nodeType: "CalculateNode",
            config: JSON.stringify(c),
          };
        }),
        layouts: this.layouts.map((l) => {
          return {
            id: l.id,
            layout: JSON.stringify(l),
          };
        }),
      }));
      this.updateDebounce = null;
    }, 300);
  }
}
