import React from "react";
import { Form, Select, Button, Input } from "antd";

const FormItem = Form.Item;
const SelectOption = Select.Option;

const DataSourceForm = (props) => {
  const [form] = Form.useForm();

  const onFinish = (values) => {
    props.onFinish(values);
    form.resetFields();
  };

  const initialValues = {
    type: "mysql",
  };

  return (
    <div className="data-source-form-container">
      <div className="caption">Add data source</div>
      <Form
        form={form}
        className="data-source-form"
        layout="vertical"
        onFinish={onFinish}
        initialValues={initialValues}
      >
        <FormItem name="type" label="Data source type">
          <Select>
            <SelectOption value="mysql">MySQL</SelectOption>
            <SelectOption value="postgres">PostgreSQL</SelectOption>
          </Select>
        </FormItem>
        <FormItem name="name" label="Name">
          <Input placeholder="Enter data source name" />
        </FormItem>
        <FormItem name="host" label="Host">
          <Input placeholder="Enter data source host" />
        </FormItem>
        <FormItem name="port" label="Port">
          <Input placeholder="Enter data source port" />
        </FormItem>
        <FormItem name="database" label="Database name">
          <Input placeholder="Enter database name" />
        </FormItem>
        <FormItem name="username" label="Username">
          <Input placeholder="Enter database username" />
        </FormItem>
        <FormItem name="password" label="Password">
          <Input type="password" placeholder="Enter database password" />
        </FormItem>
        <FormItem>
          <Button>Test connectivity</Button>
        </FormItem>
        <FormItem>
          <Button type="primary" htmlType="submit">
            Add data source
          </Button>
        </FormItem>
      </Form>
    </div>
  );
};

export default DataSourceForm;
