export const safeAssign = (stageConfig, groupKey, propertyKey, value) => {
  if (!propertyKey) {
    return {
      ...stageConfig,
      [groupKey]: value,
    };
  }

  // if group key exists
  if (stageConfig[groupKey]) {
    return {
      ...stageConfig,
      [groupKey]: {
        ...stageConfig[groupKey],
        [propertyKey]: value,
      },
    };
  }

  // group key does not exists
  return {
    ...stageConfig,
    [groupKey]: {
      [propertyKey]: value,
    },
  };
};

export const safeGet = (config, groupKey, propertyKey) => {
  const groupValue = config[groupKey];
  if (typeof groupValue === "object" && groupValue !== null) {
    return groupValue[propertyKey];
  }

  return undefined;
};

export const trimStartDollarSign = (columnName) => {
  if (columnName.startsWith("$")) {
    return columnName.split("$")[1];
  }
  return columnName;
};

export const getAuthStatusFromLocalStorage = () => {
  const authStatus = localStorage.getItem("authed");
  if (authStatus) {
    return JSON.parse(authStatus);
  }

  return false;
};

/**
 * 
 * Creates a timer that accepts a `timerCalc` function to perform
 * calculated timeout retries, such as exponential backoff.
 *
 * Copied from [phoenix.js](https://github.com/phoenixframework/phoenix/blob/master/assets/js/phoenix.js)
 * 
 * @example
 * let reconnectTimer = new Timer(() => this.connect(), function(tries){
 *   return [1000, 5000, 10000][tries - 1] || 10000
 * })
 * reconnectTimer.scheduleTimeout() // fires after 1000
 * reconnectTimer.scheduleTimeout() // fires after 5000
 * reconnectTimer.reset()
 * reconnectTimer.scheduleTimeout() // fires after 1000
 *
 * @param {Function} callback
 * @param {Function} timerCalc
 */
 export class Timer {
  constructor(callback, timerCalc){
    this.callback  = callback
    this.timerCalc = timerCalc
    this.timer     = null
    this.tries     = 0
  }

  reset(){
    this.tries = 0
    clearTimeout(this.timer)
  }

  /**
   * Cancels any previous scheduleTimeout and schedules callback
   */
  scheduleTimeout(){
    clearTimeout(this.timer)

    this.timer = setTimeout(() => {
      this.tries = this.tries + 1
      this.callback()
    }, this.timerCalc(this.tries + 1))
  }
}
