import React from "react";
import PropTypes from "prop-types";
import ConfigInput from "./input";
import ConfigArrayInput from "./array-input";
import ConfigSelect from "./select";
import ObjectInput from './object-input';
import "./index.less";

const ConfigGroup = ({ group, node, onValueChange }) => {
  let content = null;

  switch (group.type) {
    case "input":
      content = (
        <ConfigInput
          group={group}
          value={node.transformer.process[group.key]}
          onValueChange={onValueChange("input")}
        />
      );
      break;

    case "select":
      content = (
        <ConfigSelect
          group={group}
          onValueChange={onValueChange("select")}
          node={node}
        />
      );
      break;

    case "array":
      if (group.items.type === "string") {
        content = (
          <ConfigArrayInput
            group={group}
            value={node.transformer.process[group.key]}
            onValueChange={onValueChange("arrayinput")}
          />
        );
      }
      break;

    case "object":
      content = <ObjectInput group={group} node={node} onValueChange={onValueChange} />
      break;

    default:
      break;
  }
  return <div className="config-group">{content}</div>;
};

ConfigGroup.propTypes = {
  group: PropTypes.object.isRequired,
  node: PropTypes.object.isRequired,
  onValueChange: PropTypes.func,
};

export default ConfigGroup;
