import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { Menu, Dropdown } from 'antd';

import './index.less';

const aggFuncOptions = {
  "filter": "No aggregate function",
  "sum": "SUM",
};

const operatorOptions = {
  "in": "In",
  "between": "Between",
  "==": "Equal to",
  ">=": "Greater than or equal to",
  ">": "Greater than",
  "<=": "Less than or equal to",
  "<": "Less than",
};

export default class AddPredictionLabel extends Component {
  render() {
    const { selectedFilter, cancelEdit, addEmptyCondition, cancelEmptyCondition } = this.props;

    return (
      <div className="add-prediction-label">
        <div className="add-prediction-label__label">Add new prediction label</div>
        <div className="add-prediction-label__form-block">
          <div className="add-prediction-label__form-block--label">Name</div>
          <input className="add-prediction-label__form-block--input" placeholder="Enter a name for this label" />
        </div>

        <div className="add-prediction-label__form-block">
          <div className="add-prediction-label__form-block--label">Table</div>

          <Dropdown overlay={ this._renderTableOptions(0) }  trigger={['click']}>
            <div className="add-prediction-label__form-block--select">
              <div className="add-prediction-label__form-block--select-value">
                { selectedFilter.tableName || "Select table" }
              </div>
              <div className="add-prediction-label__form-block--select-icon">
                <i className="iconfont iconvector-down" />
              </div>
            </div>
          </Dropdown>
        </div>

        <div className="add-prediction-label__form-block">
          <div className="add-prediction-label__form-block--label">Column</div>

          <Dropdown overlay={ this._renderColumnOptions(0) }  trigger={['click']}>
            <div className="add-prediction-label__form-block--select">
              <div className="add-prediction-label__form-block--select-value">
                { (selectedFilter && selectedFilter.conditions[0].columnName) || "Select column" }
              </div>
              <div className="add-prediction-label__form-block--select-icon">
                <i className="iconfont iconvector-down" />
              </div>
            </div>
          </Dropdown>

          <Dropdown overlay={ this._renderAggFuncOptions(0) }  trigger={['click']}>
            <div className="add-prediction-label__form-block--select">
              <div className="add-prediction-label__form-block--select-value">
                { (selectedFilter && selectedFilter.conditions[0]) ? (aggFuncOptions[selectedFilter.conditions[0].aggregate] || "filter") : "SUM" }
              </div>
              <div className="add-prediction-label__form-block--select-icon">
                <i className="iconfont iconvector-down" />
              </div>
            </div>
          </Dropdown>

          <Dropdown overlay={ this._renderOperatorOptions(0) }  trigger={['click']}>
            <div className="add-prediction-label__form-block--select">
              <div className="add-prediction-label__form-block--select-value">
                { (selectedFilter && selectedFilter.conditions[0]) ? operatorOptions[selectedFilter.conditions[0].operator] : "Equal to" }
              </div>
              <div className="add-prediction-label__form-block--select-icon">
                <i className="iconfont iconvector-down" />
              </div>
            </div>
          </Dropdown>

          <input className="add-prediction-label__form-block--input" placeholder="Value" value={ selectedFilter && selectedFilter.conditions[0].value } onChange={ this.setValue } />
        </div>

        {
          selectedFilter && selectedFilter.conditions.length > 1 ?
            selectedFilter.conditions.slice(1, selectedFilter.conditions.length).map((c, cIdx) => (
              <div key={cIdx}>
                <div className="add-prediction-label__form-block">
                  <div className="add-prediction-label__form-block--select disabled">
                    <div className="add-prediction-label__form-block--select-value">
                      OR
                    </div>
                    <div className="add-prediction-label__form-block--select-icon">
                      <i className="iconfont iconvector-down" />
                    </div>
                  </div>
                </div>

                <div className="add-prediction-label__form-block">
                  <div className="add-prediction-label__form-block--label">Column</div>
                  <Dropdown overlay={ this._renderColumnOptions(cIdx+1) }  trigger={['click']}>
                    <div className="add-prediction-label__form-block--select">
                      <div className="add-prediction-label__form-block--select-value">
                        { c.columnName || "Select column" }
                      </div>
                      <div className="add-prediction-label__form-block--select-icon">
                        <i className="iconfont iconvector-down" />
                      </div>
                    </div>
                  </Dropdown>

                  <Dropdown overlay={ this._renderAggFuncOptions(cIdx + 1) }  trigger={['click']}>
                    <div className="add-prediction-label__form-block--select">
                      <div className="add-prediction-label__form-block--select-value">
                        { c.agregation || "No aggregate func" }
                      </div>
                      <div className="add-prediction-label__form-block--select-icon">
                        <i className="iconfont iconvector-down" />
                      </div>
                    </div>
                  </Dropdown>

                  <Dropdown overlay={ this._renderOperatorOptions(cIdx + 1) }  trigger={['click']}>
                    <div className="add-prediction-label__form-block--select">
                      <div className="add-prediction-label__form-block--select-value">
                        { c.operator || "==" }
                      </div>
                      <div className="add-prediction-label__form-block--select-icon">
                        <i className="iconfont iconvector-down" />
                      </div>
                    </div>
                  </Dropdown>

                  <input className="add-prediction-label__form-block--input" placeholder="Value" value={ c.value } />
                </div>
              </div>
            )):
            null
        }

        <div className="add-prediction-label__add-btn" onClick={ addEmptyCondition }>
          <i className="iconfont iconplus" />
          Add condition
        </div>

        <div className="add-prediction-label__btn-group">
          <div className="add-prediction-label__btn-group--btn cancel" onClick={ cancelEdit }>Cancel</div>
          <div className="add-prediction-label__btn-group--btn confirm">Add filter</div>
        </div>
      </div>
    );
  }

  _renderTableOptions = () => (
    <Menu onClick={ this.chooseTable }>
      <Menu.Item key="Orders">
        Orders
      </Menu.Item>
      <Menu.Item key="Products">
        Products
      </Menu.Item>
      <Menu.Item key="Users">
        Users
      </Menu.Item>
    </Menu>
  );

  _renderColumnOptions = (cIdx) => (
    <Menu onClick={ this.chooseColumn(cIdx) }>
      <Menu.Item key="Order_date">
        Order_date
      </Menu.Item>
      <Menu.Item key="purchase">
        purchase
      </Menu.Item>
      <Menu.Item key="Status">
        Status
      </Menu.Item>
      <Menu.Item key="Name">
        Name
      </Menu.Item>
      <Menu.Item key="Version">
        Version
      </Menu.Item>
    </Menu>
  );

  _renderAggFuncOptions = (cIdx) => (
    <Menu onClick={ this.chooseAggFunc(cIdx) }>
      {
        Object.keys(aggFuncOptions).map((k) => (
          <Menu.Item key={k}>
            { aggFuncOptions[k] }
          </Menu.Item>
        ))
      }
    </Menu>
  );

  _renderOperatorOptions = (cIdx) => (
    <Menu onClick={ this.chooseOperator(cIdx) }>
      {
        Object.keys(operatorOptions).map((k) => (
          <Menu.Item key={k}>
            { operatorOptions[k] }
          </Menu.Item>
        ))
      }
    </Menu>
  );

  chooseTable = (e) => {
    this.props.updateTable(e.key);
  }

  chooseAggFunc = (cIdx) => (e) => {
    this.props.updateFilter("aggregation", cIdx, e.key);
  }

  chooseColumn = (cIdx) => (e) => {
    this.props.updateFilter("columnName", cIdx, e.key);
  }

  chooseOperator = (cIdx) => (e) => {
    this.props.updateFilter("operator", cIdx, e.key);
  }

  setValue = (cIdx) => (e) => {
    this.props.updateFilter("value", cIdx, e.key);
  }
};

AddPredictionLabel.propTypes = {
  selectedRow: PropTypes.array,
  cancelEdit: PropTypes.func.isRequird,
  addEmptyCondition: PropTypes.func.isRequired,
  cancelEmptyCondition: PropTypes.func.isRequired,
  updateTable: PropTypes.func.isRequired,
  updateCondition: PropTypes.func.isRequired,
  updateFilter: PropTypes.func.isRequired,
};
