import { observable, action, computed, makeObservable } from 'mobx';
import update from 'immutability-helper';
import { groupBy, cloneDeep } from 'lodash';
import { v1 as uuidv1 } from 'uuid';

import json from '../label_logic.js';

const emptyFilter = {
  logicOperator: "AND",
  tableId: "",
  tableName: "",
  conditions: [
    {
      columnId: "",
      columnName: "",
      aggregation: "",
      operator: "",
      value: "",
      logicOperator: "OR",
    }
  ]
};

export default class QueriesStore {
  @observable queries = json.filters;
  @observable selectedFilterId = null;

  constructor() {
    makeObservable(this);
  }

  @action
  selectFilter = (filterId) => () => {
    this.selectedFilterId = filterId;
  }

  @action
  addEmptyFilter = () => {
    const newFilterId = uuidv1();
    this.queries.push({...cloneDeep(emptyFilter), id: newFilterId});

    this.selectedFilterId = newFilterId;
  }

  @action
  addEmptyCondition = () => {
    const filterIdx = this.queries.map((f) => f.id).indexOf(this.selectedFilterId);

    this.queries = update(this.queries, {
      [filterIdx]: {
        conditions: {
          $push: [{
            columndId: "",
            columnName: "",
            aggregation: "",
            operator: "",
            value: "",
            logicOperator: "OR",
          }]
        }
      }
    });
  }

  @action
  updateTable = (table) => {
    this.queries = update(this.queries, {
      [this.selectedFilterId]: {
        tableName: {
          $set: table
        }
      }
    });
  }

  @action
  updateFilter = (key, cIdx, value) => {
    const filterIdx = this.queries.map((f) => f.id).indexOf(this.selectedFilterId);

    this.queries = update(this.queries, {
      [filterIdx]: {
        conditions: {
          [cIdx]: {
            [key]: {
              $set: value
            }
          }
        }
      }
    });
    console.error(this.queries);
  }

  @action
  cancelEmptyCondition = () => {
  }

  @action
  cancelEdit = () => {
    this.selectedFilterId = null;
  }

  @action
  deleteFilter = (filterId) => () => {
    this.queries = this.queries.filter((f) => f.id != filterId);
    this.selectedFilterId = null;
  }

  @action
  duplicateFilter = (filterId) => () => {
    const filter = this.queries.find((f) => f.id == filterId);
    this.queries.push(cloneDeep({...filter, id: uuidv1()}));
  }

  @computed
  get selectedTable() {
    const table = this.queries[this.selectedRowTId];
    return !!table ? table.table : null;
  }

  @computed
  get selectedFilter() {
    return this.queries.find((f) => f.id == this.selectedFilterId);
  }

  @computed
  get parsedQueries() {
    return groupBy(this.queries, (f) => f.tableName);
  }
}
