import React, { useState } from "react";
import PropTypes from "prop-types";
import { UpCircleOutlined, DownCircleOutlined } from "@ant-design/icons";
import ConfigInput from "./input";
import ConfigArrayInput from "./array-input";
import ConfigSelect from "./select";
import ConfigMultiSelect from "./multi-select";
import AggregateFunctions from "./aggregate-functions";
import { safeGet } from "../../utils/common";
import FixedArrayInput from "./fixed-array-input";
import ConditionGroups from "./condition-groups";

const ObjectInput = ({ group, node, onValueChange }) => {
  const [collapsed, setCollapsed] = useState(!group.required);

  const showDetail = () => {
    setCollapsed(false);
  }

  const hideDetail = () => {
    setCollapsed(true);
  }

  if (collapsed) {
    return (
      <div className="object-group">
        <div className="object-group__heading">
          <div className="object-group__heading__caption">
            {group.label.toUpperCase()}
          </div>
          <div className="object-group__heading__toggle">
            <DownCircleOutlined onClick={showDetail} />
          </div>
        </div>
      </div>
    );
  }

  let objectContent = group.properties.map((p, i) => {
    if (
      !!p.relatedInfo &&
      node.transformer.process[group.key][p.relatedInfo.key] !=
        p.relatedInfo.value
    ) {
      return null;
    }

    if (p.type == "input") {
      return (
        <ConfigInput
          key={i}
          groupKey={group.key}
          property={p}
          value={safeGet(node.transformer.process, group.key, p.key)}
          onValueChange={onValueChange("input")}
        />
      );
    }

    if (p.type == "select") {
      return (
        <ConfigSelect
          key={i}
          groupKey={group.key}
          property={p}
          onValueChange={onValueChange("select")}
          node={node}
        />
      );
    }

    if (p.type == "multiselect") {
      return (
        <ConfigMultiSelect
          key={i}
          groupKey={group.key}
          property={p}
          value={safeGet(node.transformer.process, group.key, p.key)}
          onValueChange={onValueChange("multiselect")}
        />
      );
    }

    if (p.type === "array") {
      if (p.items.type === "AggregateFunction") {
        return (
          <AggregateFunctions
            groupKey={group.key}
            property={p}
            value={safeGet(node.transformer.process, group.key, p.key)}
            onValueChange={onValueChange("AggregateFunction")}
          />
        );
      }

      if (p.items.type === "number" || p.items.type === "string") {
        return (
          <ConfigArrayInput
            group={group}
            property={p}
            value={safeGet(node.transformer.process, group.key, p.key)}
            onValueChange={onValueChange("arrayinput")}
          />
        );
      }

      if (p.items.type === "ConditionGroup") {
        return (
          <ConditionGroups
            groupKey={group.key}
            property={p}
            value={safeGet(node.transformer.process, group.key, p.key)}
            onValueChange={onValueChange(p.items.type)}
          />
        );
      }
    }

    if (p.type == "FixedArrayInput") {
      return (
        <FixedArrayInput
          groupKey={group.key}
          property={p}
          value={safeGet(node.transformer.process, group.key, p.key)}
          onValueChange={onValueChange("FixedArrayInput")}
        />
      );
    }
  });

  return (
    <div className="object-group">
      <div className="object-group__heading">
        <div className="object-group__heading__caption">
          {group.label.toUpperCase()}
        </div>
        <div className="object-group__heading__toggle">
          <UpCircleOutlined onClick={hideDetail} />
        </div>
      </div>
      {objectContent}
    </div>
  );
};

ObjectInput.propTypes = {
  group: PropTypes.object.isRequired,
  node: PropTypes.object.isRequired,
  onValueChange: PropTypes.func,
};

export default ObjectInput;
