import React, { Component } from "react";
import PropTypes from "prop-types";
import { safeAssign } from "../../utils/common";
import ConfigGroup from "../../components/ConfigGroup";
import "./index.less";

class ProcessConfigPanel extends Component {
  render() {
    let { node, formConfig } = this.props;

    return (
      <div className="process-config-panel">
        <div className="process-config-panel__desc">
          Below are the node process configurations
        </div>

        {formConfig.map((group, i) => (
          <ConfigGroup
            key={i}
            group={group}
            node={node}
            onValueChange={this.onValueChange}
          />
        ))}
      </div>
    );
  }

  onValueChange = (type) => (group, key) => (eventOrValue) => {
    let { node } = this.props;
    var nextProcessConfig = {};

    switch (type) {
      case "select":
        nextProcessConfig = safeAssign(
          node.transformer.process,
          group,
          key,
          eventOrValue.item.props.value !== undefined
            ? eventOrValue.item.props.value
            : eventOrValue.key
        );
        break;

      case "input":
        nextProcessConfig = safeAssign(
          node.transformer.process,
          group,
          key,
          eventOrValue.target.value
        );
        break;

      case "multiselect":
        nextProcessConfig = safeAssign(
          node.transformer.process,
          group,
          key,
          eventOrValue
        );
        break;

      case "AggregateFunction":
        nextProcessConfig = safeAssign(
          node.transformer.process,
          group,
          key,
          eventOrValue
        );
        break;

      case "ConditionGroup":
        nextProcessConfig = safeAssign(
          node.transformer.process,
          group,
          key,
          eventOrValue
        );
        break;

      case "arrayinput":
        nextProcessConfig = safeAssign(
          node.transformer.process,
          group,
          key,
          eventOrValue
        );
        break;

      case "FixedArrayInput":
        nextProcessConfig = safeAssign(
          node.transformer.process,
          group,
          key,
          eventOrValue
        );
        break;

      default:
        break;
    }

    this.props.updateNodeTransformerSetting("process", nextProcessConfig);
  };
}

ProcessConfigPanel.propTypes = {
  node: PropTypes.object.isRequired,
  config: PropTypes.object.isRequired,
  formConfig: PropTypes.array.isRequired,
  findNodeById: PropTypes.func.isRequired,
  updateNodeTransformerSetting: PropTypes.func.isRequired,
};

export default ProcessConfigPanel;
