import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { makeObservable, observable, action, computed } from 'mobx';
import { observer } from 'mobx-react';

import './index.less';

@observer
class KeepOrDropColumnSetting extends Component {
  @observable currentConfig = {
    type: "keep",
  };

  constructor() {
    super();
    makeObservable(this);
  }

  render() {
    let { cancel, config } = this.props;

    return (
      <div className="keey-or-drop-column-setting">
        <div className="setting-radios">
          <div className="setting-radios__radio-btn">
            <input id="keep" type="radio" name="keep_or_drop" value="keep" defaultChecked={ config.type == "keep" } />
            <label htmlFor="keep">Keep</label>
          </div>

          <div className="setting-radios__radio-btn">
            <input id="drop" type="radio" name="keep_or_drop" value="drop" defaultChecked={ config.type == "drop" } />
            <label htmlFor="drop">Drop</label>
          </div>
        </div>

        <div className="setting-block">
          <div className="setting-label">Table</div>
          <input className="setting-input" placeholder="Select table" ref="table" defaultValue={ config.table } />
        </div>

        <div className="setting-block">
          <div className="setting-label">Column</div>
          <input className="setting-input" placeholder="Set column" ref="column" defaultValue={ config.column } />
        </div>

        <div className="setting-btns">
          <div className="setting-btns__cancel" onClick={ cancel }>Cancel</div>
          <div className="setting-btns__add" onClick={ this.added }>Add</div>
        </div>
      </div>
    );
  }

  added = () => {
    let data = {
      ...this.currentConfig,
      table: this.refs["table"].value,
      column: this.refs["column"].value,
    };

    this.props.added(`${this.currentConfig.type}_column`)(data);
  }
}

KeepOrDropColumnSetting.propTypes = {
  cancel: PropTypes.func.isRequired,
  added: PropTypes.func.isRequired,
};

export default KeepOrDropColumnSetting;
