import React from "react";
import axios from "axios";
import { Form, Input, Button } from "antd";
import { inject, observer } from "mobx-react";
import { Link } from "react-router-dom";
import "./index.less";

const FormItem = Form.Item;

const Login = (props) => {
  const history = props.routingStore;
  const [form] = Form.useForm();
  const forgetPassword = (
    <Link to="/forget-password">I forgot my password</Link>
  );

  const handleLogin = (params) => {
    let data = new FormData();
    data.append("username", params.email);
    data.append("password", params.password);
    axios.post("/api/user/login", data).then((res) => {
      props.userStore.stompWebsocket.connect(() => {
        props.userStore.signIn();
        history.push("/");
      });
    });
  };

  if (props.userStore.authed) {
    history.push("/");
  }

  return (
    <div className="login">
      <div className="login__left-panel">
        <div className="login__left-panel--logo">
          <img src="images/logo.png" />
        </div>

        <div className="login__left-panel--slogan">
          Build predictions models, get all insights from your company data{" "}
          <b>flexibly</b>.
        </div>

        <img className="login__left-panel--tree-bg" src="images/tree.svg" />
        <div className="login__left-panel--lines-bg" />
        <div className="login__left-panel--bottom-lines-bg" />

        <div className="login__left-panel--footer" />
      </div>

      <div className="login__right-panel">
        <div className="login__form-container">
          <div className="login__form-container__header">
            Login to your account
          </div>

          <Form
            className="login__form-container__body"
            layout="vertical"
            requiredMark={"optional"}
            form={form}
            onFinish={handleLogin}
          >
            <FormItem label="Email" name="email" rules={[{ required: true }]}>
              <Input placeholder="Enter your email" />
            </FormItem>
            <FormItem
              label="Password"
              name="password"
              rules={[{ required: true }]}
              extra={forgetPassword}
            >
              <Input type="password" placeholder="Enter your password" />
            </FormItem>
            <FormItem>
              <Button type="primary" htmlType="submit">
                Login
              </Button>
            </FormItem>
          </Form>
        </div>
      </div>
    </div>
  );
};

export default inject("routingStore", "userStore")(observer(Login));
