import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { makeObservable, observable, action } from 'mobx';

import VisualizedLabelDefinition from '../../components/VisualizedLabelDefinition';
import DefinePrediction from '../../components/DefinePrediction';
import AddPredictionLabel from '../../components/AddPredictionLabel';

import './index.less';

@inject('queriesStore')
@observer
class Labels extends Component {
  render() {
    const { queriesStore } = this.props;

    return (
      <div className="labels">
        <section className="labels__main">
          <div className="labels__main--content">
            <VisualizedLabelDefinition
              queries={ queriesStore.parsedQueries }
              deleteFilter={ queriesStore.deleteFilter }
              duplicateFilter={ queriesStore.duplicateFilter }
              selectFilter={ queriesStore.selectFilter }
              selectedFilterId={ queriesStore.selectedFilterId }
              addEmptyFilter={ queriesStore.addEmptyFilter }
            />
          </div>

          <div className="labels__main--sidebar">
            {
              queriesStore.selectedFilterId != null ?
                <AddPredictionLabel
                  selectedFilter={ queriesStore.selectedFilter }
                  cancelEdit={ queriesStore.cancelEdit }
                  addEmptyCondition={ queriesStore.addEmptyCondition }
                  cancelEmptyCondition={ queriesStore.cancenlEmptyCondition }
                  updateTable={ queriesStore.updateTable }
                  updateCondition={ queriesStore.updateCondition }
                  updateFilter={ queriesStore.updateFilter }
                />:
                <DefinePrediction
                />
            }
          </div>
        </section>
      </div>
    );
  }
}

export default Labels;
