import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { makeObservable, observable, action, computed } from 'mobx';
import { observer } from 'mobx-react';

import { Menu, Dropdown } from 'antd';

import AddNewColumnSetting from './AddNewColumnSetting';
import FilterColumnSetting from './FilterColumnSetting';
import RenameColumnSetting from './RenameColumnSetting';
import KeepOrDropColumnSetting from './KeepOrDropColumnSetting';

import './index.less';

const CONFIG_PANELS = {
  "add_column": "Add new column",
  "filter_column": "Filter column",
  "rename_column": "Rename column",
  "keep_or_drop_column": "Keep/Drop column",
};

@observer
class ConfigSettingPanel extends Component {
  @observable currentSettingPanel = "add_column";

  constructor() {
    super();
    makeObservable(this);
  }

  render() {
    return (
      <div className="config-setting-panel">
        <Dropdown overlay={ this._renderMenu() }  trigger={['click']}>
          <div className="config-setting-panel__switch-tab-bar">
            <span>{ this.currentSettingPanelName }</span>
            <i className="iconfont iconvector-down" />
          </div>
        </Dropdown>

        <div className="config-setting-panel__panel-body">
          { this._renderSettingPanels() }
        </div>
      </div>
    );
  }

  _renderSettingPanels = () => {
    let { cancel, added, config, configType } = this.props;
    if (configType == "keep_column" || configType == "drop_column") {
      configType = "keep_or_drop_column";
    }

    switch(configType || this.currentSettingPanel) {
      case "add_column":
        return <AddNewColumnSetting config={ config } cancel={ cancel } added={ added } />;
        break;
      case "filter_column":
        return <FilterColumnSetting config={ config } cancel={ cancel } added={ added } />;
        break;
      case "rename_column":
        return <RenameColumnSetting config={ config } cancel={ cancel } added={ added } />;
        break;
      case "keep_or_drop_column":
        return <KeepOrDropColumnSetting config={ config } cancel={ cancel } added={ added } />;
        break;
    }
  }

  _renderMenu = () => (
    <Menu onClick={this.selectConfigPanels}>
      <Menu.Item key="add_column">
        { CONFIG_PANELS["add_column"] }
      </Menu.Item>
      <Menu.Item key="filter_column">
        { CONFIG_PANELS["filter_column"] }
      </Menu.Item>
      <Menu.Item key="rename_column">
        { CONFIG_PANELS["rename_column"] }
      </Menu.Item>
      <Menu.Item key="keep_or_drop_column">
        { CONFIG_PANELS["keep_or_drop_column"] }
      </Menu.Item>
    </Menu>
  )

  selectConfigPanels = (e) => {
    this.currentSettingPanel = e.key;
  }

  @computed
  get currentSettingPanelName() {
    let { configType } = this.props;
    if (configType == "keep_column" || configType == "drop_column") {
      configType = "keep_or_drop_column";
    }
    return CONFIG_PANELS[configType || this.currentSettingPanel];
  }
}

ConfigSettingPanel.propTypes = {
  config: PropTypes.object.isRequired,
  configType: PropTypes.string,
  cancel: PropTypes.func.isRequired,
  added: PropTypes.func.isRequired,
};

export default ConfigSettingPanel;
