import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './index.less';

class AddNewColumnSetting extends Component {
  render() {
    let { cancel, config } = this.props;

    return (
      <div className="add-new-column-setting">
        <div className="setting-block">
          <div className="setting-label">Table</div>
          <input className="setting-input" ref="table" defaultValue={ config.table } />
        </div>

        <div className="setting-block">
          <div className="setting-label">Column name</div>
          <input className="setting-input" ref="column" defaultValue={ config.column } />
        </div>

        <div className="setting-block">
          <div className="setting-label">Column formula</div>
          <input className="setting-input" ref="formula" defaultValue={ config.formula } />
        </div>

        <div className="setting-btns">
          <div className="setting-btns__cancel" onClick={ cancel }>Cancel</div>
          <div className="setting-btns__add" onClick={ this.added }>Add</div>
        </div>
      </div>
    );
  }

  added = () => {
    let formData = {
      table: this.refs["table"].value,
      column: this.refs["column"].value,
      formula: this.refs["formula"].value
    }

    this.props.added("add_column")(formData);
  }
}

AddNewColumnSetting.propTypes = {
  added: PropTypes.func.isRequired,
  cancel: PropTypes.func.isRequired,
  config: PropTypes.object.isRequired,
};

export default AddNewColumnSetting;
