import React, { Component } from "react";
import PropTypes from "prop-types";
import AlertBar from "../AlertBar";
import "./index.less";

class Workflow extends Component {
  render() {
    const { currentProject, layouts, editProjectDetail } = this.props;

    return (
      <div className="workflow">
        <div className="workflow__header">
          <div className="workflow__header--left-block">
            <div className="workflow__header--project-name" onClick={ () => editProjectDetail(true) }>{ currentProject.project && currentProject.project.name }</div>
          </div>

          <div className="workflow__header--right-block">
            <div className="workflow__header--btn-group disabled">Train</div>
          </div>
        </div>

        <div className="workflow__alert-bar">
          <AlertBar
            title="Project created"
            desc="To start creating your workflow, drag and drop any of the objects from the workflow toolbox."
            type="succeed"
          />
        </div>

        <div className="workflow__container with-alert">
          {layouts.length > 0 ? null : (
            <div className="workflow__container-placeholder">
              <div className="workflow__container-placeholder--title">
                Start workflow
              </div>
              <div className="workflow__container-placeholder--desc">
                Drag and drop the nodes from the toolbox and start your
                predictive pipleine
              </div>
            </div>
          )}

          <div id="workflow-canvas" className="workflow__container--canvas" />

          <div className="workflow__container--toolbar">
            <div className="workflow__container--toolbar-item">
              <i className="iconfont iconzoomout" />
            </div>

            <div className="workflow__container--toolbar-item">
              <i className="iconfont iconzoomin" />
            </div>

            <div
              className="workflow__container--toolbar-item"
              onClick={this.props.showModal}
            >
              <i className="iconfont iconupload" />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Workflow.propTypes = {
  connectNodes: PropTypes.func.isRequired,
  selectNode: PropTypes.func.isRequired,
  addNewNode: PropTypes.func.isRequired,
  removeNodes: PropTypes.func.isRequired,
  updateLayouts: PropTypes.func.isRequired,
  layouts: PropTypes.array.isRequired,
  showModal: PropTypes.func.isRequired,
  currentProject: PropTypes.object.isRequired,
};

export default Workflow;
