import React, { Component } from 'react';
import PropTypes from 'prop-types';

import AlertBar from '../AlertBar';

import './index.less';

class VisualizedLabelDefinition extends Component {
  render() {
    const { queries, selectedFilterId } = this.props;
    const { deleteFilter, duplicateFilter, selectFilter, addEmptyFilter } = this.props;

    return (
      <div className="label-definition">
        <div className="label-definition__header">
          <div className="label-definition__header--left-block">
            <div className="label-definition__header--project-name">Project #1</div>
          </div>

          <div className="label-definition__header--right-block">
            <div className="label-definition__header--btn-group disabled">
              Train
            </div>
          </div>
        </div>

        <div className="label-definition__alert-bar">
          <AlertBar
            title='Project created'
            desc='To start creating your workflow, drag and drop any of the objects from the workflow toolbox.'
            type='succeed'
          />
        </div>

        <div className="label-definition__container with-alert">
          <div>
            {
              Object.keys(queries).map((t, ti) => {
                return (
                  <div className="label-definition__logic-table" key={`table-${ti}`}>
                    <div className="label-definition__logic-column">
                      <div className="label-definition__logic-item table">{ t }</div>
                    </div>

                    <div className="label-definition__logic-column">
                      {
                        queries[t].map((f, fi) => {
                          return (
                            <div className={`label-definition__logic-row ${ f.logicOperator }`} key={`row-${fi}`}>
                              <div className="label-definition__logic-block">
                                <div className={`label-definition__logic-item ${f.conditions[0].aggregation || "filter"}`}>
                                  { `${f.conditions[0].columnName} ${f.conditions[0].operator} ${f.conditions[0].value}` }
                                </div>

                                {
                                  f.conditions.slice(1, f.conditions.length).map((c, ci) => {
                                    return (
                                      <div className={`label-definition__logic-row ${ c.logicOperator }`} key={`row-sub-${ci}`}>
                                        <div className={`label-definition__logic-item ${ c.aggregation || "filter" } `}>
                                          { `${c.columnName} ${c.operator} ${c.value}` }
                                        </div>
                                      </div>
                                    );
                                  })
                                }
                              </div>
                              <div className="label-definition__hover-layer">
                                <div className={`label-definition__hover-layer--container ${ selectedFilterId == f.id ? "selected" : "" }`}>
                                  <div className="label-definition__hover-layer--toolbar">
                                    <i className="iconfont iconcopy" onClick={ duplicateFilter(f.id) } />
                                    <i className="iconfont icondelete" onClick={ deleteFilter(f.id) } />
                                    <i className="iconfont iconpencil" onClick={ selectFilter(f.id) } />
                                  </div>
                                </div>
                              </div>
                            </div>
                          );
                        })
                      }
                    </div>
                  </div>
                );
              })
            }
          </div>

          <div className="label-definition__container--add-filter" onClick={ addEmptyFilter }>
            <i className="iconfont iconplus" />
            Add filter
          </div>
        </div>
      </div>
    );
  }
}

VisualizedLabelDefinition.propTypes = {
  queries: PropTypes.object.isRequired,
  deleteFilter: PropTypes.func.isRequired,
  duplicateFilter: PropTypes.func.isRequired,
  selectFilter: PropTypes.func.isRequired,
  selectedFilterId: PropTypes.number,
  addEmptyFilter: PropTypes.func.isRequired,
};

export default VisualizedLabelDefinition;
